// =====================================================================
// User
// =====================================================================
.user {
  // display: none;

  &.user-not-login {
    display: block;
  }

  &.user-login {
    display: none;
  }

  &.registered-user {
    display: none;
  }

  &.user-dropdown {
    padding-right: 5px;

    @include media-breakpoint-up(lg) {
      align-items: center;
      padding-right: 0;
    }

    .user-state {
      img {
        margin-right: 4px;
      }

      &.user-login {

      }
    }

    .no-touchevents & {
      &:hover {
        // background-color: $color-whitepale-light;
        box-shadow: none;

        @include media-breakpoint-up(lg) {
          background-color: transparent;
        }
      }
    }

    .dropdown {
      .no-touchevents & {
        &:hover {
          .dropdown-toggle {
            background-color: transparent;
          }
        }
      }
    }

    .dropdown-toggle {
      text-align: left;

      @include media-breakpoint-up(lg) {
        padding-top: 0;
        padding-bottom: 0;
      }

      &[aria-expanded='true'] {
        .welcome,
        .username,
        .fa-caret-down {
          @include media-breakpoint-down(md) {
            color: $white;
          }
        }
      }

      .welcome {
        color: $color-brand;

        @include media-breakpoint-up(lg) {
          color: $color-grey;
        }
      }

      .username {
        color: $color-brand;

        @include media-breakpoint-up(lg) {
          color: $color-brand;
        }
      }

      .fa-caret-down {
        color: $color-brand;
        display: block;

        @include media-breakpoint-up(lg) {
          display: none;
        }
      }

      &::after {
        content: '';
        display: none;
      }
    }

    .topnav-link {
      padding: 0;

      @include media-breakpoint-up(lg) {
        @include font(14, $letterSpacing: $font-ls-small);
        line-height: 16px;

        &:hover {
          color: $color-brand;
        }
      }

      .username-not-verify {
        margin: 5px;
        height: 9px;
        width: 9px;
        border-radius: 50%;
        background-color: $color-red-shinmin;
        display: none;
      }
    }

    .dropdown-item {
      padding-top: 10px;
      padding-bottom: 10px;
      color: $color-grey;
      text-align: left;

      @include media-breakpoint-up(lg) {
        text-align: center;
      }
    }

    .divider {
      width: calc(100% - 8px);
      height: 1px;
      margin-left: auto;
      margin-right: auto;
      background-color: $color-grey-lighter;
    }

    .dropdown-menu {
      width: 300px; //100%;
      background-color: $background-tertiary;
      // display: block; // for coding
      // right: 0;
      // left: unset;

      @include media-breakpoint-up(lg) {
        margin-bottom: 0;
        box-shadow: 0 0 4px 0 rgba(58, 58, 58, 0.5);
        top: 45px;
      }
    }

    .topnav-submenu {
      white-space: normal;
      column-count: auto;
      padding: 0;

      @include media-breakpoint-up(lg) {
        padding-top: 10px;
        // padding-bottom: 10px;
      }

      .topnav-link {
        padding: 0;
      }
    }

    .user-warning {
      color: $color-red-shinmin;
      display: none;

      img {
        padding-right: 1rem;
      }
    }
  }
}
//desktop profile dashbaord
.dashboard {
  padding-top: 14px;

  .dashboard-header {
    p {
      @include font (16);
      font-weight: 500;
      // margin-bottom: 0
    }
  }

  .dashboard-subheader {
    p {
      @include font (14);
      color: $color-grey-light;
      // margin-bottom: 0;
    }
  }

  p {

  }

  .dashboard-list {
    margin: 16px 32px 0;

    li {
      display: flex;
      flex-direction: row;
      align-items: center;
      text-align: left;
      margin-bottom: 8px;

      img {
        margin-right: 8px;
      }

      p {
        @include font(14);
        margin-bottom: 0;
      }

      a {
        border-bottom: 1px solid $color-whitepale;
        margin-bottom: 8px;
        padding-bottom: 16px;
        width: 100%;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .dashboard-login {
    padding-top: 24px;
  }

  &.user-login,
  &.user-subscriber {
    padding: 14px 0 0;

    .dashboard-header {
      padding: 0 16px;
      display: flex;
      align-items: center;

      img {
        width: 60px;
      }

      p {
        text-align: left;
      }

      .profile-state {
        text-align: left;
        margin-left: 16px;

        .label {
          @include font (14);
          letter-spacing: 1px;
          font-weight: 500;
        }
      }
    }

    .dashboard-list {
      margin: 16px 0 0;
      padding: 0 16px;
    }
  }

  .dashboard-title {
    padding: 8px 16px 0;
  }

  .dashboard-msg {
    margin-top: 24px;
    padding: 16px 0;
    background-color: $color-white;

    p {
      color: $color-grey-light;
      @include font (14);
      margin-bottom: 0;
    }

    a {
      @include font (14);
      color: $content-quaternary;
      border-bottom: 1px solid $content-quaternary;
    }
  }
}
