// =====================================================================
// Layout
// =====================================================================

$row-tighten: 10px;

.row-tighten {
  @include media-breakpoint-up(lg) {
    margin-left: -$row-tighten;
    margin-right: -$row-tighten;

    > [class*='col-'] {
      padding-left: $row-tighten;
      padding-right: $row-tighten;
    }
  }
}

%l-topnav-link-padding-x {
  padding-left: 10px;
  padding-right: 10px;
}

%l-topnav-link-padding-y {
  padding-top: 9px;
  padding-bottom: 9px;
}

%l-link-article {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid $color-grey-lighter;

  &:last-of-type {
    border-bottom: 0;
  }
}

%l-widget-spacing {
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;

  @include media-breakpoint-up(lg) {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

%l-aside-item {
  background-color: $color-whitepale-light;

  @include media-breakpoint-up(lg) {
    margin-top: 16px;

    // &:first-of-type {
    //   margin-top: 0;
    // }
  }
}

%l-aside-primary-maxw {
  margin-left: auto;
  max-width: 300px;

  @include media-breakpoint-up(xl) {
    max-width: 330px;
  }
}

%l-aside-item-padding-x {
  padding-left: 5px;
  padding-right: 5px;
}

%l-aside-item-padding-y {
  padding-top: 10px;
  padding-bottom: 10px;
}

%l-inline-m {
  margin-top: 10px;
  margin-bottom: 10px;
}

%l-footer-main-mb {
  margin-bottom: 30px;

  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
  }
}

%l-outbrain-mb {
  margin-bottom: 8px;
}
