// ========================================================================
// MIXINS
// ========================================================================

@mixin truncate {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin lineclamp($linenumber) {
  display: -webkit-box;
  -webkit-line-clamp: $linenumber;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@mixin clearfix {
  &::after {
    content: '';
    clear: both;
    display: table;
  }
}

@mixin bordered-gradient($from, $to, $bg) {
  &::before {
    background: linear-gradient(to right, $from, $to);
  }

  &::after {
    background-color: $bg;
  }
}

@mixin reset-button {
  background: none;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  cursor: pointer;
}

@mixin reset-input {
  margin: 0;
  border: 0;
  padding: 0;
  display: inline-block;
  vertical-align: middle;
  white-space: normal;
  background: none;
  line-height: 1;
}

@mixin reset-list {
  padding-left: 0;
  margin-bottom: 0;
  list-style-type: none;
}

@mixin revert-list {
  display: block;
  list-style-type: disc;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
  padding-left: 40px;

  > li {
    display: list-item;
  }
}

// https://maximilianhoffmann.com/posts/better-font-rendering-on-osx
@mixin font-smoothing($value: on) {
  @if $value == on {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  @else {
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: auto;
  }
}

// @include font(21, 24, 20)
@mixin font($sizeValue: 16, $lineHeight: null, $letterSpacing: null, $isEm: false) {
  @if type-of($sizeValue) == 'string' {
    font-size: $sizeValue;
  }

  @else {
    font-size: $sizeValue + px;

    @if ($isEm) {
      font-size: em($sizeValue);

      // p {
      //   font-size: em(10); // unset compounding
      // }
    }

    @else {
      font-size: ($sizeValue / 10) + rem;
    }
  }

  @if type-of($lineHeight) != null {
    line-height: $lineHeight;
  }

  @if type-of($letterSpacing) != null {
    letter-spacing: $letterSpacing;
  }
}

@mixin hamburger($width, $height, $thickness, $linecolor) {
  position: relative;
  width: $width;
  height: $height;

  > div {
    position: absolute;
    left: unset;
    background-color: $linecolor;
    width: 100%;
    height: $thickness;
    margin: 0 2px;

    &:nth-child(1) {
      top: 3px;
    }

    &:nth-child(2) {
      top: 50%;
      margin-top: -($thickness / 2);
    }

    &:nth-child(3) {
      bottom: 3px;
    }
  }
}

// @include aspect-ratio(16, 9);
@mixin aspect-ratio($width, $height) {
  width: 100%;

  &::before {
    content: '';
    width: 1px;
    margin-left: -1px;
    float: left;
    height: 0;
    padding-top: ($height / $width) * 100%;
  }

  &::after {
    content: '';
    display: table;
    clear: both;
  }
}

// @include loading($w: 10px, $border-w: 2px, $spd: 1.3s, $primary: red, $secondary: green);
@mixin loading($w: 24px, $border-w: 4px, $spd: 0.9s, $primary: darken(#ffffff, 45%), $secondary: darken(#ffffff, 25%)) {
  width: $w;
  height: $w;
  margin-top: -($w / 2);
  margin-left: -($w / 2);
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  border-radius: 50%;
  border-style: solid;
  border-width: $border-w;
  border-top-color: $primary;
  border-left-color: $secondary;
  border-bottom-color: $secondary;
  border-right-color: $secondary;
  animation: spinning $spd linear infinite;
}

// @include btn-cancel(20px, 100%, 1px, transparentize($color-black, 0.5));
@mixin btn-cancel($length, $cross-length, $cross-thickness, $cross-color) {
  position: relative;
  width: $length;
  height: $length;
  cursor: pointer;

  &::before,
  &::after {
    top: calc(50% - (#{$cross-thickness / 2}));
    content: '';
    position: absolute;
    left: (100 - $cross-length) / 2;
    width: $cross-length;
    height: $cross-thickness;
    background-color: $cross-color;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

// @include underline(0.5, -2px);
@mixin underline($thickness, $offset) {
  position: relative;
  text-decoration: none;

  &::after {
    content: '';
    position: absolute;
    bottom: $offset;
    left: 0;
    width: 100%;
    height: 0.1em * $thickness;
    background-color: currentColor;
  }
}

// @include underline-border(0.5, -2px);
@mixin underline-border($thickness, $offset) {
  position: relative;
  text-decoration: none;
  padding-bottom: ($offset + em);
  border-bottom: ($thickness + px) solid currentColor;

  &:hover,
  &:focus,
  &:visited {
    text-decoration: none;
  }
}
