// =====================================================================
// Overwrites
// =====================================================================

.custom-control {
  padding-left: 2rem;
  cursor: pointer;
}

.custom-control-label {
  &::before,
  &::after {
    left: -2rem;
    top: 0.7rem;
    width: 1.25rem;
    height: 1.25rem;
  }
}

.container {
  &.no-padding-l {
    padding-left: 0;
  }

  &.no-padding-r {
    padding-right: 0;
  }
}

.slick-lightbox-slick-caption {
  @include font-notosanssc-regular;
  @include font(18);
  display: block;
  padding: 5px 0;
}

.slick-lightbox-slick-item {
  outline: none;
}

.shepherd-modal-mask-opening {
  rx: 4px;
  ry: 4px;
}

.shepherd {
  .shepherd-text {
    @include font(18);
  }

  .shepherd-button {
    background: $color-brand;
    color: $color-white;
    @include font(12);
    border-radius: 15px;

    &:hover {
      background: $color-brand-light;
    }

    &.shepherd-button-secondary {
      background: $color-grey-light;
      color: $color-white;

      &:hover {
        background: $color-whitepale;
        color: $color-grey;
      }
    }
  }

  .shepherd-footer {
    padding: 0 15px 15px 15px;

    .shepherd-button {
      &:first-child {
        margin-left: auto;
      }

      &:last-child {
        margin-right: auto;
      }
    }
  }
}
