// readers club
.readers-club-dashboard {
  border-radius: 8px;
  margin-bottom: 40px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
  // display: none;

  // @include media-breakpoint-up (lg) {
  //   display: block;
  // }

  @include min-screen (1440px) {
    margin-bottom: initial;
    height: 306px;
  }

  &.subscriber {
    background-image: url('./../images/common/readerclub-background.jpg');

    h3 {
      color: $color-white;
    }

    p {
      color: $color-white;
      letter-spacing: normal;
    }
  }

  &.registered-subscriber {
    background-image: url('./../images/common/register-user-bkg.jpg');

    h3 {
      color: $color-grey;
    }

    p {
      color: $color-grey;
      letter-spacing: normal;
    }
  }

  &.visitor {
    background-image: url('./../images/common/visitor-bkg.jpg');

    h3 {
      color: $color-grey;
    }

    p {
      color: $color-grey;
      letter-spacing: normal;
    }
  }

  .readers-club-header {
    border-radius: 8px 8px 0 0;
    color: $color-white;
    padding: 5px 16px 0;
    position: relative;
    // height: initial;

    @include media-breakpoint-up (md) {
      // height: 73px;
    }

    @include media-breakpoint-up (xl) {
      height: 73px;
    }

    .readers-club-header-content {
      display: block;
      text-align: center;
      padding-bottom: 10px;

      p {
        margin: 0 auto;
      }

      @include min-screen (360px) {
        p {
          margin: 0 auto; // 0 21%;
        }
      }

      .welcome-note {
        display: block;
        font-weight: 500;
        // @include media-breakpoint-up(sm) {
        //   display: none;
        // }
        &::after {
          content: '';
          width: 100px;
          background-color: white;
          height: 1px;
          display: block;
          margin: 4px auto;
          @include media-breakpoint-up(sm) {
            display: none;
          }
        }

        &.for-subscriber,
        &.for-member {
          display: block;
          @include media-breakpoint-up(sm) {
            display: none;
          }
        }
      }

      @include media-breakpoint-up(sm) {
        text-align: left;
        padding-bottom: 10px;
        display: inline-block;

        p {
          margin: initial;
        }
      }

      @include media-breakpoint-up(lg) {
        padding-bottom: initial;
      }

      .readers-club-header-content-img-container {
        text-align: center;

        @include media-breakpoint-up (sm) {
          text-align: left;
        }

        padding-bottom: 4px;

        img {
          width: 110px;
          @include media-breakpoint-up (lg) {
            width: 113px;
          }
        }

        .reader-club-img {
          display: none;
        }
      }
    }

    .readers-club-tablist {
      justify-content: center;
      border-bottom: none;

      @include media-breakpoint-up (md) {
        justify-content: end;
      }

      @include media-breakpoint-up(lg) {
        float: right;
        justify-content: end;
        margin-top: 34px;// 23px;
      }

      .nav-tabs {
        border-bottom: none;
        width: 83px;
        position: relative;
        overflow: hidden;

        // @include min-screen (321px) {
        //   width: 83px;
        // }

        @include min-screen (360px) {
          width: 98px;
        }

        @include media-breakpoint-up (sm) {
          width: 130px;
        }

        .nav-link {
          background: $color-whitepale;
          border-radius: 8px 8px 0 0;
          border: none;
          margin: 0 auto;
          text-align: center;
          font-weight: 400;
          position: relative;
          width: 70px;
          padding: 0.5rem;
          // height: 34px;
          max-height: 34px;
          color: $color-grey-light;
          @include font(10);
          // padding: 7px 5px 6px;
          display: flex;
          justify-content: center;
          align-items: center;

          @include min-screen(360px) {
            @include font(12);
            width: 83px;
            padding: 0.5rem 1rem;
          }

          @include media-breakpoint-up(sm) {
            padding: 0.5rem 1rem;
            width: 114px;
            @include font(16);
          }

          // @include media-breakpoint-up (lg) {
          //   width: 114px;
          //   height: unset;
          //   padding: 7px 0 6px;
          //   align-items: unset;
          //   @include font(14);
          // }

          // p {
          //   color: $color-grey-light;
          //   margin-bottom: 0;
          //   line-height: 1;
          //   @include font(12);

          //   @include media-breakpoint-up (lg) {
          //     @include font(14);
          //     line-height: normal;
          //   }
          // }

          .arrow-down {
            width: 0;
            height: 0;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-top: 5px solid $color-grey-light;
            display: inline-block;
            position: relative;
            margin-left: 4px;
            top: -2px;

            @include media-breakpoint-up (lg) {
              display: none;
            }
          }

          &.subscriber {
            background: $color-pink;
            color: $color-white;

            // p {
            //   color: $color-white;
            //   margin-bottom: 0;
            // }

            .arrow-down {
              width: 0;
              height: 0;
              border-left: 4px solid transparent;
              border-right: 4px solid transparent;
              border-top: 5px solid $color-white;
              display: inline-block;
              position: relative;
              top: -2px;
              margin-left: 4px;

              @include media-breakpoint-up (lg) {
                display: none;
              }
            }

            &::before {
              content: '';
              display: block;
              border-radius: 50%;
              height: 16px;
              width: 16px;
              position: absolute;
              bottom: 0;
              left: -16px;
              box-shadow: 8px 8px 0 0 $color-pink;
            }

            &::after {
              content: '';
              display: block;
              border-radius: 50%;
              height: 16px;
              width: 16px;
              position: absolute;
              bottom: 0;
              right: -16px;
              box-shadow: -8px 8px 0 0 $color-pink;
            }

            &.active {
              color: $color-pink;

              // p {
              //   color: $color-pink;
              //   @include font (12);

              //   @include media-breakpoint-up (lg) {
              //     @include font(14);
              //   }
              // }

              @include media-breakpoint-up (lg) {
                @include font(19);
                padding: 2px 1rem 0.5rem;
              }

              .arrow-up {
                width: 0;
                height: 0;
                border-left: 4px solid transparent;
                border-right: 4px solid transparent;
                border-bottom: 5px solid $color-pink;
                display: inline-block;
                position: relative;
                top: -2px;
                margin-left: 4px;

                @include media-breakpoint-up (lg) {
                  display: none;
                }
              }
            }
          }

          &.visitor {
            background: $color-whitepale-light;

            &::before {
              box-shadow: 8px 8px 0 0 $color-whitepale-light;
            }

            &::after {
              box-shadow: -8px 8px 0 0 $color-whitepale-light;
            }
          }

          &::before {
            content: '';
            display: block;
            border-radius: 50%;
            height: 16px;
            width: 16px;
            position: absolute;
            bottom: 0;
            left: -16px;
            box-shadow: 8px 8px 0 0 $color-whitepale;
          }

          &::after {
            content: '';
            display: block;
            border-radius: 50%;
            height: 16px;
            width: 16px;
            position: absolute;
            bottom: 0;
            right: -16px;
            box-shadow: -8px 8px 0 0 $color-whitepale;
          }

          &.active {
            background: $color-white;
            color: $color-grey;
            font-weight: 600;

            @include media-breakpoint-up (lg) {
              @include font(19);
              padding: 2px 1rem 0.5rem;
            }

            &::before {
              content: '';
              display: block;
              border-radius: 50%;
              height: 16px;
              width: 16px;
              position: absolute;
              bottom: 0;
              left: -16px;
              box-shadow: 8px 8px 0 0 $color-white;
            }

            &::after {
              content: '';
              display: block;
              border-radius: 50%;
              height: 16px;
              width: 16px;
              position: absolute;
              bottom: 0;
              right: -16px;
              box-shadow: -8px 8px 0 0 $color-white;
            }

            .arrow-up {
              width: 0;
              height: 0;
              border-left: 4px solid transparent;
              border-right: 4px solid transparent;
              border-bottom: 5px solid $color-grey;
              display: inline-block;
              position: relative;
              top: -2px;
              margin-left: 4px;

              @include media-breakpoint-up (lg) {
                display: none;
              }
            }

            // p {
            //   color: $color-grey;
            // }
          }
        }

        // p {
        //   font-size: 14px;
        // }
      }

      &.subscriber-tab,
      &.member-tab {
        @include media-breakpoint-up (lg) {
          // margin-top: 63px;
        }
      }

      // &.member-tab {
      //   @include media-breakpoint-up (lg) {
      //     margin-top: 63px;
      //   }
      // }
    }
  }

  .readers-club-content {
    clear: both;
    background: $color-white;
    min-height: 233px;
    border-radius: 16px 16px 8px 8px;
    position: relative;
    // padding-bottom: 24px;
    display: none;
    @include media-breakpoint-up (lg) {
      display: block;
    }

    .reader-club-benefits {
      padding: 30px 16px;
      text-align: center;

      @include media-breakpoint-up (sm) {
        padding: 30px 30px 24px;
      }

      p {
        color: $color-grey;

        &.caption {
          @include font(12);
          color: $color-grey-light;

          a {
            color: $color-grey-light;
          }
        }

        a {
          @include font(12);
          border-bottom: 1px solid  $color-grey-light;
          // color: $color-grey-light;

          &:hover {
            text-decoration: none;
          }
        }
      }

      .benefits-listing {
        list-style-type: none;
        padding-left: 0;
        display: flex;
        justify-content: center;
        flex-flow: wrap;
        width: 100%;

        @include media-breakpoint-up (sm) {
          justify-content: space-around;
        }

        @include media-breakpoint-up(md) {
          // justify-content: space-around;
        }

        li {
          // width: 33%;
          // margin: 0 8px;
          width: 27%;
          margin: 0 8px;

          @include media-breakpoint-up(md) {
            // width: 80px;
            // margin: 0 8px;

            // &:first-child {
            //   margin-left: 0;
            // }

            // &:last-child {
            //   margin-right: 0;
            // }
          }

          @include media-breakpoint-up(lg) {
            width: 75px;
            // margin: 0 8px;

            &:first-child {
              margin-left: 0;
            }

            &:last-child {
              margin-right: 0;
            }
          }

          text-align: center;

          p {
            padding-top: 8px;
            color: $color-grey;
            @include font (12);

            &.inactive {
              color: $color-grey-lighter;
            }
          }
        }
      }

      .readers-club-cta {
        border-top: 1px solid $color-whitepale;
        padding-top: 24px;
        // padding-bottom: 40px;
        display: block;

        @include media-breakpoint-up (md) {
          display: flex;
          justify-content: center;
          align-items: center;
          padding-bottom: initial;
        }

        p {
          margin-bottom: 0;
          padding-bottom: 16px;

          @include media-breakpoint-up (md) {
            padding-bottom: initial;
          }

          &.no-padding-bottom {
            padding-bottom: 0;
          }
        }

        .cta {
          @include font(16);
          letter-spacing: 1px;
          padding: 0 16px;
          min-width: 99px;
          margin-left: 0.875rem;
        }

        &.user {
          display: none;
          @include media-breakpoint-up (sm) {
            display: block;
          }
        }
      }
    }

    &.anonymous-user {
      display: block;
    }

    // &.readers-club-content-mobile {
    //   display: none;
    //   @include media-breakpoint-up (lg) {
    //     display: block;
    //   }
    // }

    .cta-cancel {
      position: absolute;
      top: 15px;
      right: 15px;
      height: 20px;
      width: 20px;
      display: block;
      // padding: 5px;
      // border-radius: 50%;
      // border: 1px solid $color-brand;
      @include media-breakpoint-up (lg) {
        display: none;
      }

      img {
        width: 100%;
      }
    }
  }

  .reader-club-modal {
    position: absolute;

    .small {
      @include font (12);

      &.light-grey {
        color: $color-grey-light;
      }
    }

    .modal-dialog {
      background-color: rgba(43, 43, 43, 0.9);
      max-width: 100%;
      margin: 48% 3.5rem;
      border-radius: 8px;

      @include media-breakpoint-up (sm) {
        margin: 200px 3.5rem;
      }

      @include media-breakpoint-up (md) {
        max-width: 290px;
        margin: 63px auto;
      }

      @include media-breakpoint-up (lg) {
        margin: 63px auto;
      }

      .modal-content {
        background: none;
        border: none;
        color: $color-white;
        padding: 16px 32px;

        .modal-header {
          border: none;
          padding: 0;
          display: block;
          text-align: center;

          p {
            color: $color-grey-lighter;
            border-bottom: 1px solid $color-grey-lighter;
            display: inline-block;
          }
        }

        .modal-body {
          padding: 0;
          margin: 0 auto;

          ul {
            list-style-type: none;
            padding-left: 0;

            li {
              display: flex;
              align-content: center;
              padding-bottom: 16px;

              .context {
                padding-left: 16px;

                p {
                  margin-bottom: 0;
                  color: $color-white;

                  &.light-grey {
                    color: $color-grey-lighter;
                  }
                }
              }
            }
          }
        }

        .modal-footer {
          padding: 0;
          display: block;
          text-align: center;
          border: none;

          .cta {
            margin: 0 auto;
            height: 20px;
            @include font (12);
            line-height: normal;
            letter-spacing: normal;
            padding: 0 16px;
          }
        }
      }
    }

    &.show {
      .modal-dialog {
        .model-content {
          border: none;
          background: rgba(43, 43, 43, 0.9);
          border-radius: 8px;
          width: 290px;
        }
      }
    }
  }
}

// readers-club-promo
.readers-club-promo {
  // border-radius: 8px;
  overflow: hidden;
  height: auto;
  // aspect-ratio: 8 / 5;
  margin-bottom: 16px;

  @include media-breakpoint-up (md) {
    // aspect-ratio: 8 / 5;
    margin-bottom: 30px;
  }

  @include min-screen(1440px) {
    // height: 306px;
    // aspect-ratio: unset;
  }

  .single-promo {
    width: 100%;
    // border-radius: 8px;
    text-align: center;
    position: relative;

    @include min-screen(1440px) {
      height: 306px;
    }

    .single-promo-img {
      // width: 100%;
      position: relative;
      // padding-top: 55.5%;
      object-position: center;
      overflow: hidden;

      @include min-screen (1680px) {
        height: 254px;
      }

      img {
        // position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        border-radius: 8px;
      }
    }

    .gradient {
      position: absolute;
      background-image: linear-gradient(180deg, rgba(70, 70, 70, 0) 0%, rgba(34, 34, 34, 0.8) 100%);
      width: 100%;
      bottom: 0;
      height: 39%;
      border-radius: 0 0 8px 8px;

      @include media-breakpoint-up (md) {
        height: 16%;
        bottom: 0;
      }

      @include media-breakpoint-up (md) {
        height: 20%;
      }

      @include min-screen(1440px) {
        bottom: 0;
        height: 20%;
      }
    }

    figcaption {
      padding-top: 1rem;

      p {
        margin-bottom: 0;
      }
    }
  }
  // overwrite slick dots
  .slick-dots {
    top: 72%;

    // @include max-screen (32px) {
    //   top: 65%;
    // }

    @include min-screen (375px) {
      top: 75%;
    }

    @include min-screen (425px) {
      top: 77%;
    }

    @include media-breakpoint-up(sm) {
      top: 80%;
    }

    @include media-breakpoint-up (md) {
      top: 82%;
    }

    @include media-breakpoint-up (lg) {
      top: 87%;
    }

    @include min-screen (1280px) {
      top: 90%;
    }

    @include min-screen(1440px) {
      top: 71%;
    }

    li {
      margin: 0;

      button {
        &::before {
          font-size: 8px;
          color: $color-white;
          opacity: 1;
        }
      }

      &.slick-active {
        button {
          &::before {
            color: $color-brand;
          }
        }
      }
    }
  }
}

.sec {
  &.readers-club-faq {
    padding-top: 38px;

    .cta-faq {
      margin-right: 0;

      &:hover {
        color: $color-grey-light;
      }

      img {
        width: 14px;
        margin-left: 8px;
      }
    }

    .heading-border {
      &.heading-border-primary {
        h2 {
          padding-top: 0;
          // @include media-breakpoint-up(lg) {
          //   padding-top: 38px;
          // }
        }
      }

      .heading-border-right-content {
        padding-top: 0;
        padding-bottom: 11px;
      }
    }

    .readers-club-faq-content {
      padding-top: 20px;

      @include media-breakpoint-up(lg) {
        padding-top: 0;
      }

      .faq {
        padding-bottom: 1.75rem;

        p {
          margin-bottom: 0;
        }

        a {
          border-bottom: 1px solid $color-grey;

          &:hover {
            text-decoration: none;
            border-bottom: 1px solid $color-brand;
          }
        }

        &:last-child {
          padding-bottom: 0;
        }
      }
    }
  }

  &.readers-club-enquiry {
    padding-top: 38px;

    .heading-border {
      &.heading-border-primary {
        h2 {
          padding-top: 0;
        }
      }

      .heading-border-right-content {
        padding-top: 0;
      }
    }

    .article-type {
      padding-top: 20px;
    }
  }
}

.readers-club-gift {
  position: fixed;
  bottom: 120px;
  left: 0;
  border-radius: 0 45px 45px 0;
  background: $color-whitepale-light;
  min-height: 84px;
  min-width: 100px;
  padding-left: 14px;
  display: none;

  @include media-breakpoint-up(md) {
    display: block;
  }

  a {
    &:hover {
      text-decoration: none;
    }

    p {
      padding-top: 4px;
      @include font(12);
      color: $color-brand;
      font-weight: 500;
    }
  }
}

.readers-club-mobile {
  margin-right: 16px;

  img {
    width: 22px;
    height: auto;
  }
}

.reader-promo-container {
  @include make-col-ready();
  padding: 0 15px;

  @include min-screen (1440px) {
    padding: 0 10px 0 15px;
    @include make-col(4);
  }
}

.reader-dashboard-container {
  @include make-col-ready();
  padding: 0 15px;

  @include min-screen (1440px) {
    padding: 0 15px 0 10px;
    @include make-col(8);
  }
}

.reader-club-listing {
  .nav-tabs {
    border: none;
    align-items: flex-start;

    .nav-tab {
      padding-top: 2px;

      &:first-child {
        padding-top: 0;
      }

      .heading-border-sublink {
        color: $color-grey-light;
        padding-top: 2px;

        &.active {
          color: $color-grey;
        }
      }

      .heading-border {
        &.heading-border-primary {
          h2 {
            padding-top: 0;
          }
        }
      }

      a {
        @include font(16);
      }
    }
  }
}

.readers-club-deals {
  .heading-border {
    .heading-border-right-content {
      padding-top: 0;
      @include media-breakpoint-up (sm) {
        padding-top: 6px;
      }
    }
  }
}

.readers-club-activites {
  padding-top: 38px;

  .heading-border {
    .heading-border-right-content {
      padding-top: 0;
      @include media-breakpoint-up (sm) {
        padding-top: 6px;
      }
    }
  }

  .heading-border-mainlink {
    h2 {
      padding-top: 0;
    }

    &:hover {
      h2 {
        &::before {
          transition: opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic, height 0.4s $easeOutCubic;
          content: none;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          background-color: $color-brand;
          height: 0;
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}
