// =====================================================================
// Topnavbar (Secondary)
// =====================================================================

.topnavbar-secondary {
  z-index: 2;
  background-color: $color-white;
  // border-top: 1px solid $color-whitepale;
  // border-bottom: 1px solid #a80034;

  a {
    @include font($font-base-size);
  }

  .dropdown {
    > .topnav-link {
      padding: 0;

      > span {
        @extend %l-topnav-link-padding-x;
        @extend %l-topnav-link-padding-y;
      }
    }
  }

  .topnav-link {
    @extend %l-topnav-link-padding-y;

    @include media-breakpoint-up(lg) {
      @include font(14);
    }

    @include media-breakpoint-up(xl) {
      @include font(16);
    }

    &:hover {
      // color: $color-white;
    }

    &.dropdown-item {
      &:hover {
        color: $color-brand;
      }
    }

    &.logo {
      display: none;
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  .topnav-submenu {
    > .topnav-link {
      @include font(16);
    }
  }

  .cta-holder {
    display: none;
    margin-left: 10px;
  }

  .fa-caret-down {
    margin-left: 10px;
  }

  .svg-icon-search {
    max-width: 15px;
    fill: $color-grey;
  }

  .cta-holder-subscribe {
    a {
      @include media-breakpoint-up(lg) {
        @include font(14);
      }

      @include media-breakpoint-up(xl) {
        @include font(16);
      }
    }
  }

  .topnavbar-search {
    display: flex;
    justify-content: flex-end;

    form {
      width: auto;
      position: relative;

      .searchbar {
        position: relative;
        width: 100%;
        // height: 32px;
      }

      input[type='search'] {
        height: 32px;
        // padding-left: 38px;

        &::placeholder {
          @include font (16);
        }
      }
    }
  }

  // navbar line cosmetic
  .topnavbar-secondary-border {
    border-top: 1px solid $color-whitepale;
    border-bottom: 1px solid #a80034;
  }
}
