// =====================================================================
// Cxense
// =====================================================================

$logo-col-spacing: 40px;

.cxense {
  @extend %l-widget-spacing;

  position: relative;
  width: 100%;
  border-style: solid;
  border-width: 2px;
  border-radius: 16px;
  padding: 40px 10px 30px;

  .legend {
    position: absolute;
    top: -6px;
    left: 0;
    width: 100%;
    display: block;
    text-align: center;

    .legend-label {
      @include font(18, $letterSpacing: $font-ls-regular);
      @include font-notosanssc-medium;

      border-radius: 50px;
      display: inline-block;
      background-color: $color-grey;
      padding: 3px 18px;
      color: $color-white;
      margin-top: -16px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .logo-zaobao {
    margin-left: auto;
    margin-right: auto;

    @include media-breakpoint-up(xl) {
      max-width: 75px;
    }

    @include media-breakpoint-up(xxl) {
      max-width: 90px;
    }
  }

  &.primary {
    border-color: $color-pink-light;

    .legend-label {
      background-color: $color-pink;
      color: $color-white;
    }
  }

  &.secondary {
    border-color: $color-yellow;

    .legend-label {
      background-color: $color-yellow;
      color: $color-white;
    }
  }

  .max-size {
    max-width: 100%;
    margin: auto;
  }

  &.promo {
    .cta {
      display: inline-block;
    }

    .cir-promo-banner-link {
      position: relative;
      width: 100%;
      height: auto;
      display: none;

      .promo-banner-container {
        position: absolute;
        width: 172px;
        height: 179px;
        top: 0;
        right: 0;
        background-position: center;
        background-size: cover;
      }

      .promo-banner {
        width: 100%;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
      }
    }
  }

  &::before {
    border-radius: 15px;
    height: 30px;
    max-width: 120px;

    @include font-notosanssc-bold;
    @include font(18, 30px);

    text-align: center;
    position: absolute;
    top: -15px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
  }

  h2 {
    margin-bottom: 0;

    @include font(24, $letterSpacing: 2px);

    @include media-breakpoint-up(md) {
      @include font(30, $letterSpacing: 2px);

      text-align: justify;
    }
  }

  .nowrap {
    white-space: nowrap;
  }

  .left-space {
    display: flex;
    align-items: center;
    position: absolute;
    padding-left: 0;
    padding-right: 0;
    bottom: 15px;
    right: 15px;
    max-width: 35px;

    @include media-breakpoint-up(md) {
      position: relative;
      padding-left: $logo-col-spacing;
      padding-right: $logo-col-spacing;
      bottom: auto;
      right: auto;
      max-width: none;
    }
  }

  .right-space {
    padding-left: 0;
    padding-right: 0;

    @include media-breakpoint-up(md) {
      padding-left: $logo-col-spacing;
      padding-right: $logo-col-spacing;
    }
  }

  .summary-list {
    margin: 20px 0;
    padding: 0;
    float: left;

    > li {
      @include font(16);

      color: $color-grey;
      letter-spacing: 1px;
      padding-left: 10px;
      list-style: none;
      background-image: url('./../images/icon/icon-circle-solid.svg');
      background-repeat: no-repeat;
      background-position: left 10px;
      background-size: 5px 5px;
    }
  }

  .newsletter-cont {
    input {
      &::placeholder {
        @include font(14, $letterSpacing: $font-ls-small);
      }
    }
  }

  .foot-notes {
    display: block;
    width: 100%;
    padding-bottom: 15px;

    @include media-breakpoint-up(md) {
      padding-bottom: 0;
    }

    p {
      @include font(16, $letterSpacing: $font-ls-small);

      a {
        @include font(16, $letterSpacing: $font-ls-small);

        color: $color-grey-light;
      }
    }
  }

  .border-left {
    border-left: 1px solid $color-grey-lighter;

    @include media-breakpoint-down(sm) {
      border-left: 0 !important; // stylelint-disable-line declaration-no-important
    }

    h2 {
      text-align: left;
    }
  }

  .valign-center {
    display: flex;
    align-items: center;
  }

  .cta {
    letter-spacing: 4px;
    min-width: 150px;
    padding: 5px 20px;
    height: auto;
    text-align: center;
    border-radius: 50px;
  }

  &.cxense1 {
    .left-space,
    .right-space {
      @include media-breakpoint-between(lg, lg) {
        padding-left: $logo-col-spacing / 2;
        padding-right: $logo-col-spacing / 2;
      }
    }
  }

  &.cxense2 {
    .left-space,
    .right-space {
      @include media-breakpoint-between(lg, lg) {
        padding-left: $logo-col-spacing / 2;
        padding-right: $logo-col-spacing / 2;
      }
    }

    li {
      list-style: none;
      background-image: none;
      padding-left: 0;
    }

    h2 {
      @include font(26, 1.4, $font-ls-small);
    }
  }

  &.cxense3 {
    h2 {
      @include font(26, 1.4, $font-ls-small);
    }

    input {
      @include font(18);

      float: left;
      padding: 4px 15px;
      appearance: none;
      border: solid 1px $color-whitepale;
      width: 100%;
      margin: 5px 0;
      border-radius: 500px;

      &::placeholder {
        color: $color-grey-lighter;
      }

      @include media-breakpoint-up(xl) {
        width: 60%;
        margin: 15px 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }

    .left-space {
      position: absolute;
      bottom: 15px;
      right: 15px;
      padding-left: 0;
      padding-right: 0;
      max-width: 35px;

      @include media-breakpoint-up(xl) {
        position: relative;
        bottom: auto;
        right: auto;
        padding-left: $logo-col-spacing;
        padding-right: $logo-col-spacing;
        max-width: none;
      }
    }

    .right-space {
      @include media-breakpoint-between(md, lg) {
        max-width: 100%;
        flex: 0 0 100%;
        margin: auto;
      }
    }

    .newsletter-cont {
      text-align: center;
    }

    .border-left {
      @include media-breakpoint-down(lg) {
        border-left: 0 !important; // stylelint-disable-line declaration-no-important
      }
    }

    button {
      display: inline-block;

      @include font(18, $letterSpacing: $font-ls-small);

      padding: 4px 15px;
      height: auto;
      float: none;
      margin: 5px auto 15px;
      border-radius: 500px;

      @include media-breakpoint-up(xl) {
        float: left;
        margin: 15px 0;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }

      &::before {
        width: 0;
      }
    }
  }
}
