// =====================================================================
// Link
// =====================================================================

.link {

}

.link-underline {
  transition: color 0.3s $easeOutCubic;

  @include font($font-base-size, $letterSpacing: $font-ls-regular);

  display: inline-block;
  color: $color-grey;

  @include underline-border(0.5, 0);

  cursor: pointer;

  &:hover {
    color: $color-brand;
  }
}

.link-article {
  @extend %l-link-article;

  display: block;
  color: $color-grey;

  .img-hero {
    max-width: 100px;
    margin-right: 10px;
  }

  &:hover {
    text-decoration: none;

    .link-article-header {
      .category {
        color: $color-grey;
      }
    }

    .link-article-body {
      p {
        color: $color-brand;
      }
    }
  }

  .link-article-row {
    display: flex;
    flex-direction: row;
  }

  .link-article-col {
    max-width: 100%;
    flex: 0 1 auto;

    &.link-article-col-grow {
      flex: 1 1 auto;
    }

    .link-article-body {
      width: 100%;
      // justify-content: space-between;
      // flex-direction: row;

      .link-description {
        padding-right: 16px;

        p {
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }

      .link-image {
        img {
          width: 100%;
          aspect-ratio: 3 / 2;
          height: auto;
          border-radius: 4px;
          object-fit: cover;
          overflow: hidden;
          object-position: center;
        }
      }
    }
  }
}

.link-article-header {
  line-height: 14px;
  margin-bottom: 8px;

  span {
    display: inline-block;
    vertical-align: top;
    color: $color-grey-light;
    padding: 0 6px;
    border-right: 1px solid $color-grey-lighter;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
      border-right: 0;
    }
  }

  .category {
    transition: color 0.3s $easeOutCubic;

    @include font(14);
  }

  .time {
    @include font(12);
  }
}

.link-article-body {
  p {
    transition: color 0.3s $easeOutCubic;

    @include font(16);

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.link-login {
  color: $color-brand;

  @include underline-border(0.5, 0);
}
