//head room function

.headroom {
  will-change: transform;
  transition: transform 200ms linear;
}

.headroom--pinned {
  transform: translateY(0%);
}

.headroom--unpinned {
  transform: translateY(-100%);

  &.forSticker {
    display: none;
  }
}

.animated {
  -webkit-animation-duration: 0.5s;
     -moz-animation-duration: 0.5s;
       -o-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
     -moz-animation-fill-mode: both;
       -o-animation-fill-mode: both;
          animation-fill-mode: both;
  will-change: transform, opacity;
}

@-webkit-keyframes slideDown {
  0% {
    -webkit-transform: translateY(-100%);
  }

  100% {
    -webkit-transform: translateY(0);
  }
}

@-moz-keyframes slideDown {
  0% {
    -moz-transform: translateY(-100%);
  }

  100% {
    -moz-transform: translateY(0);
  }
}

@-o-keyframes slideDown {
  0% {
    -o-transform: translateY(-100%);
  }

  100% {
    -o-transform: translateY(0);
  }
}

@keyframes slideDown {
  0% {
    transform: translateY(-100%);
    z-index: -1;
    position: relative;
  }

  100% {
    transform: translateY(0);
    z-index: 3;
    position: relative;
  }
}

.animated {
  &.slideDown {
    -webkit-animation-name: slideDown;
    -moz-animation-name: slideDown;
    -o-animation-name: slideDown;
    animation-name: slideDown;
  }
}

@-webkit-keyframes slideUp {
  0% {
    transform: translateY(0);
    z-index: 1;
  }

  99% {
    opacity: 1;
    display: none;
  }

  100% {
    transform: translateY(-100%);
    opacity: 1;
    z-index: -1;
  }
}

@-moz-keyframes slideUp {
  0% {
    transform: translateY(0);
  }

  99% {
    opacity: 1;
  }

  100% {
    transform: translateY(-100%);
    opacity: 1;
    z-index: -1;
    display: none;
  }
}

@keyframes slideUp {
  0% {
    transform: translateY(0);
  }

  99% {
    opacity: 1;
    transform: translateY(-100%);
    display: none;
  }

  100% {
    // transform: translateY(-100%);
    // opacity: 1;
    z-index: -1;
  }
}

.animated {
  &.slideUp {
    -webkit-animation-name: slideUp;
    -moz-animation-name: slideUp;
    -o-animation-name: slideUp;
    animation-name: slideUp;
  }
}

@keyframes primaryslideDown {
  0% {
    transform: translateY(-100%);
    z-index: -1;
  }

  100% {
    transform: translateY(0);
    z-index: 3;
  }
}

@-webkit-keyframes primaryslideDown {
  0% {
    -webkit-transform: translateY(-100%);
    z-index: -1;
  }

  100% {
    -webkit-transform: translateY(0);
    z-index: 3;
  }
}

@-moz-keyframes primaryslideDown {
  0% {
    -moz-transform: translateY(-100%);
    z-index: -1;
  }

  100% {
    -moz-transform: translateY(0);
    z-index: 3;
  }
}

@-o-keyframes primaryslideDown {
  0% {
    -o-transform: translateY(-100%);
    z-index: -1;
  }

  100% {
    -o-transform: translateY(0);
    z-index: 3;
  }
}

.animated {
  &.primaryslideDown {
    -webkit-animation-name: primaryslideDown;
    -moz-animation-name: primaryslideDown;
    -o-animation-name: primaryslideDown;
    animation-name: primaryslideDown;
  }
}