.user-verify-notifier {
  background-color: #ffdde5;
  position: absolute;
  padding: 20px 0;
  z-index: 3;
  width: 100%;
  display: none;

  h4 {
    @include font(16);
    font-weight: 400;
    color: $color-red-shinmin;
  }

  img {
    padding-right: 10px;
  }

  .cta-user-verify-notifier {
    background-color: $color-red-shinmin;
    border-radius: 23px;
    color: $color-white;
    padding: 0 20px;
    height: 30px;
    letter-spacing: normal;
    margin-top: 10px;
  }

  .user-verify-notifier-holder {
    .user-verify-notifier-content {
      display: flex;
      justify-content: center;
      align-items: flex-start;
    }

    .user-verify-notifier-cta-holder {
      padding: 10px 20px 0;
      text-align: center;
    }

    .user-verify-notifier-cancel {
      padding-right: 10px;
      text-align: right;
    }
  }
}
