// =====================================================================
// Aside (aside)
// =====================================================================
$aside-item-showhide-duration: 0.5;
$aside-item-incoming-duration: 0.2;
$aside-item-incoming-delay: $aside-item-incoming-duration * 2;

.aside {

}

.aside-left {
  height: 100%;
  @include media-breakpoint-up (lg) {
    padding-left: 0;
  }

  .article-label-container,
  .article-meta  {
    display: none;
    @include media-breakpoint-up(lg) {
      display: block;
    }
  }

  .article-meta {
    margin-top: 72px;
    // background-color: lightblue;
  }

  .aside-content {
    // background-color: pink;
    overflow: unset;
  }

  .scroll-container {
    display: none;
    // background: lightgreen;
    // overflow-y: hidden;
    // .dropdown-menu {
    //   z-index: inherit;
    // }
    @include media-breakpoint-up (lg) {
      display: block;
      min-height: 596px;
    }
  }

  .social-dropdown  {
    position: sticky;
    top: 42px;
    margin-left: auto;
    width: 56px;
    border-radius: 20px;
    border: 1px solid $color-whitepale;

    .social-navbar {
      padding: 10px 0px;
      letter-spacing: 0;
      @include font (10);
      text-decoration: none;
      width: 100%;
      color: $color-grey-light;

      &:hover {
        color: $color-grey-light;
      }

      span {
        // margin: 0 auto;
        width: 100%;
        display: inline-block;
        text-align: center;

        img {
          margin-right: 4px;
        }
      }
    }
    .social-menu {
      // width: 40px;
      .dropdown-item {
        padding: 10px 18px;
      }
    }
  }
}

.aside-primary-maxw {
  @extend %l-aside-primary-maxw;

  &.avt {
    padding: 0;
    margin-bottom: 20px;
  }
}
// delete aside-primary from here
