.aside-primary {
  margin-left: unset;
  max-width: unset;
  @extend %l-aside-primary-maxw;

  @include media-breakpoint-up(lg) {
    margin-left: inherit;
    max-width: inherit;
    display: block; // newly added
  }
}

@import 'rightrail/rightrail-disable';
// @import 'rightrail/rightrail-enable';

.aside-secondary {
  transition: transform 0.5s $easeOutCubic;
  z-index: 1;
  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
  }

  .aside-social {
    .a2a_kit {
      padding: 10px 0;

      .aside-social-icon {
        display: block;
        line-height: normal;
        margin-bottom: 8px;

        &:last-of-type {
          margin-bottom: 0;
        }
      }

      a {
        display: inline-block;

        svg {
          path {
            transition: fill 0.4s $easeOutCubic;
            fill: $color-grey-lighter;
          }

          &:hover {
            path {
              fill: $color-grey;
            }
          }
        }
      }
    }
  }
}

.aside-recommended {
  margin-bottom: 10px;

  .recommended-header {
    @extend %l-aside-item-padding-x;

    transition: border-radius 0.2s $easeOutCubic, background-color 0.4s $easeOutCubic;
    border-radius: $br-large;
    background-color: $color-brand;
    padding-top: 10px;
    padding-bottom: 10px;

    &:hover {
      background-color: darken($color-brand, 5%);

      .number {
        &::before,
        &::after {
          background-color: darken($color-brand, 5%);
        }
      }
    }

    &[data-toggle='collapse'] {
      cursor: pointer;

      &[aria-expanded='true'] {
        border-top-left-radius: $br-regular;
        border-top-right-radius: $br-regular;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;

        ~ .recommended-body {
          border-bottom-right-radius: $br-regular;
          border-bottom-left-radius: $br-regular;
        }

        .recommended-close {
          display: block;
        }
      }

      &[aria-expanded='false'] {
        .recommended-open {
          display: block;
        }
      }
    }

    h2 {
      display: inline-block;
      color: $color-white;
      margin-right: 10px;
      margin-bottom: 0;
    }

    .recommended-header-label {
      @include font(14);

      margin-bottom: 0;
    }

    .recommended-close,
    .recommended-open {
      display: none;
      color: $color-white;
    }

    .number {
      display: inline-block;
      position: relative;
      width: 30px;
      height: 30px;
      text-align: center;

      span {
        position: relative;
        z-index: 1;
        color: $color-white;

        @include font($font-base-size, 30px);
      }

      &::before,
      &::after {
        transition: background-color 0.4s $easeOutCubic;
        content: '';
        position: absolute;
        border: solid $color-white 1px;
        background-color: $color-brand;
        border-radius: 2px;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
      }

      &::before {
        top: 0;
        left: 0;
      }

      &::after {
        top: 2px;
        left: 2px;
      }
    }
  }

  .recommended-body {
    @extend %l-aside-item-padding-x;

    background-color: $color-whitepale-light;
    border-bottom-right-radius: $br-large;
    border-bottom-left-radius: $br-large;
  }
}

.aside-content {
  position: relative;
  -webkit-overflow-scrolling: touch;
  // overflow: hidden;// auto;

  // for new homepage to be refractor
  .card-list-col-3 {
    margin: 0;
    padding-top: 0;

    li {
      .standard-card-container {
        .card-image-container {
          margin-right: 0;
        }

        &:first-child {
          // @include media-breakpoint-up (lg) {
          //   padding-top: 0;
          //   border-top: none;
          // }
        }

        &:last-child {
          // border-bottom: none;
        }

        &::before,
        &::after {
          display: none;
        }
      }

      &:first-child {
        .standard-card-container {
          // padding-top: 0;

          @include media-breakpoint-up (lg) {
            padding-top: 0;
            border-top: none;
          }
        }
      }

      &:nth-child(-n + 3) {
        .standard-card-container {
          border-top: 1px solid $color-whitepale;
        }
      }
    }

    &::before,
    &::after {
      display: none;
    }
  }
}

