.aside-item-next {
  position: relative;

  .aside-item-content {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &.is-bottom {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    right: 0;
    left: 0;
    bottom: 0;
  }

  .aside-item-next-link {
    color: $color-brand;
    display: block;
    text-align: center;

