}

.aside-item-recommended {
}

.aside-item-podcast {

}

.aside-item-breakingnews {
  .link-article-description {
    @include truncate;
  }
}

.aside-item-popular,
.aside-item-shared {
  background-color: transparent;

  .nav-tabs {
    border: 0;

    .nav-tab {
      position: relative;

      &::after {
        content: '';
        margin: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 1px;
        height: 50%;
        background-color: $color-grey-lighter;
      }

      &:last-of-type {
        &::after {
          display: none;
        }
      }
    }

    .nav-link,
    .nav-item {
      color: $color-grey-light;

      &.active {
        background-color: transparent;
        border-color: transparent;
        color: $color-grey;

        @include font-notosanssc-medium;
      }
    }

    .nav-link {
      transition: color 0.4s $easeOutCubic;
      padding: 0 10px;

      &:hover {
        color: $color-grey;
      }

      &:hover,
      &:focus {
        border-color: transparent;
      }
    }
  }
  // new design
  .heading-right {
    float: right;
  }
}

