.aside-item-freeform {
  .img-banner-link {
    position: relative;
    display: inline-block;

    img {
      width: 100%;
      margin-bottom: 10px;
    }

    &:last-of-type {
      img {
        margin-bottom: 0;
      }
    }
  }
}

.aside-shared-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  li {
    counter-increment: step-counter;
    display: flex;

    &::before {
      content: counter(step-counter);
      @include font (14);
      color: $color-grey-light;
      display: block;
      width: 24px;
      text-align: left;
      padding-top: 6px;
      padding-left: 4px;
      margin-right: 8px;
      margin-top: 12px;

      // @include media-breakpoint-up (md) {
      //   width: 24px;
      // }
    }

    .link-article {
      padding-top: 16px;
      padding-bottom: 16px;
      border-bottom: 1px solid $color-whitepale;
      width: 100%;

      &:hover {
        color: $color-grey;

        .link-article-body {
          p {
            color: $color-grey;
          }
        }
      }

      .link-article-col {
        .link-article-body {
          display: grid;
          grid-template-columns: 53% 47%;
        }
      }
    }

    &:last-child {
      .link-article {
        border-bottom: none;
      }
    }

    &:nth-child(1) {
      &::before {
        background-image: url('./../images/icon/brand-flame-icon.svg');
        background-repeat: no-repeat;
        background-position: 0 4px;
        color: $white;
        background-size: 20px;
        margin-top: -5px;
      }

      .link-article {
        padding-top: 0;
      }
    }

    &:nth-child(2) {
      &::before {
        background-image: url('./../images/icon/red-flame-icon.svg');
        background-repeat: no-repeat;
        background-position: 0 4px;
        color: $white;
        background-size: 20px;
      }
    }

    &:nth-child(3) {
      &::before {
        background-image: url('./../images/icon/pink-flame-icon.svg');
        background-repeat: no-repeat;
        background-position: 0 4px;
        color: $white;
        background-size: 20px;
      }
    }

    &:nth-child(n + 4) {
      &::before {
        background-image: url('./../images/icon/yellow-flame-icon.svg');
        background-repeat: no-repeat;
        background-position: 0 4px;
        color: $white;
        background-size: 20px;
      }

      .link-article {
        .link-article-row {
          .link-article-col {
            .link-article-body {
              display: grid;
              grid-template-columns: 100% 0;
            }
          }
        }
      }
    }
  }
}
