// =====================================================================
// Keywords
// =====================================================================

.keywords {
  // @extend %l-widget-spacing;
  text-align: left; //center
  margin-bottom: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;

  &::before {
    display: block;
    width: 20px;
    height: 20px;
    padding-left: 20px;
    margin-right: 16px;
    content: '';
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('./../images/icon/tag-icon.svg');
  }

  h2 {
    margin-bottom: 8px;
    color: $color-brand;
  }

  .cta {
    margin-right: 8px;
    margin-bottom: 8px;
  }

  .keywords-list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;

    @include reset-list;
    // margin-top: 10px;
    // margin-bottom: 10px;
  }

  .keywords-item {
    display: inline-block;
    // border-right: 1px solid $color-whitepale;
    // margin-bottom: 8px;
    // margin-right: 10px;

    a {
      transition: color 0.3s $easeOutCubic;
      color: $color-grey-light;
      background-color: $color-whitepale-light;
      font-weight: 500;
      margin-right: 10px;
      padding: 5px 16px;
      @include font(14);
      border-radius: 16px;

      &:hover {
        color: $color-grey;
        text-decoration: none;
      }
    }

    &:last-of-type {
      // border-right: 0;
    }
  }
}
