// =====================================================================
// Logged in
// =====================================================================

.is-logged-in {
  .user {
    display: flex;

    &.user-not-login {
      display: none;
    }

    &.user-login {
      display: block;
    }

    &.registered-user {
      display: none;
    }
  }

  .cta-holder-subscribe {
    // display: none !important; // stylelint-disable-line declaration-no-important
    #login-cta {
      display: none;
    }

    #subscribe-cta {
      display: none;
    }

    #app-cta {
      display: block;
    }
  }

  .cta-holder-user {
    display: block;
  }

  .topnavbar-login {
    display: none;
  }

  .topnavbar-user {
    display: block;
  }

  .header {
    &.header-mobile {
      .user-not-login {
        display: none;
      }

      .user-login {
        display: block;
      }

      .registered-user {
        display: none;
      }
    }
  }
}

.is-subscriber {
  .user {
    display: flex;

    &.user-not-login {
      display: none;
    }

    &.user-login {
      display: none;
    }

    &.registered-user {
      display: block;
    }
  }

  .header {
    &.header-mobile {
      .user-not-login {
        display: none;
      }

      .user-login {
        display: none;
      }

      .registered-user {
        display: block;
      }
    }
  }
}
