// =====================================================================
// Section
// =====================================================================

.sec {
  .page-home {
    padding-top: 10px;
    padding-bottom: 10px;

    @include media-breakpoint-up(lg) {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    &:first-of-type {
      padding-top: 0;
    }

    &.sec-videoseries {
      padding-top: 0;
      padding-bottom: 0;

      .group {
        margin-bottom: 0;
      }
    }
  }

  &.sec-filter {
    padding-bottom: 20px;
  }

  &.sec-search-results {
    padding-top: 25px;

    .pager {
      margin-top: 2rem;
      list-style-type: none;
      padding: 0;
      @include font (14);
      display: flex;
      justify-content: center;
      border-bottom: 3px solid #999;

      .pager-current {
        color: $color-brand;
        padding: 8px;
        // margin-bottom: 1rem;
      }

      li {
        margin-bottom: 1rem;
      }

      .pager-item {
        margin-bottom: 1rem;
        padding: 8px;
        // display: inline-block;
        a {
          color: $color-grey;
          @include font(14);
          text-decoration: none;
          // padding: 8px;
        }
      }
    }
  }

  &.sec-freeform {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: hidden;
    background-color: $color-whitepale-light;

    .banner {
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      margin: auto;
      max-width: 100%;
      background-image: url('https://via.placeholder.com/375x230');
      width: 375px;
      height: 230px;

      @include media-breakpoint-up(lg) {
        background-image: url('https://via.placeholder.com/1440x360');
        width: 1440px;
        height: 360px;
      }
    }
  }

  // .sec-epaper {
  //   background-color: #f9f9f9;
  //   display: flex;
  //   flex-wrap: nowrap;
  //   overflow-y: hidden;
  //   padding: 0 25px;

  //   &::-webkit-scrollbar,
  //   -ms-overflow-style,
  //   scrollbar-width {
  //     @include media-breakpoint-down(sm) {
  //       display: none;
  //     }
  //   }

  //   @include media-breakpoint-up(md) {
  //     padding: 30px 0 0;
  //     display: flex;
  //     justify-content: center;
  //   }

  //   @include media-breakpoint-up(lg) {
  //     padding: 30px 0 67px;
  //     display: flex;
  //     justify-content: center;
  //   }
  // }
  &.sec-banner {
    background-color: #f9f9f9;
    padding-bottom: 39px;

    @include media-breakpoint-up (lg) {
      padding-bottom: 20px;
    }
    // padding-top: 10px;
  }

  &.sec-video {
    background-color: $primary-background-inverse;
    padding-top: 40px;
  }
}
