// ==== newsletter sign up ===========
.newsletter-signup-holder {
  padding-top: 30px;
  overflow: hidden;

  .newsletter-signup-header {
    @include media-breakpoint-down(sm) {
      width: 100%;
      display: flex;
      justify-content: flex-start;
    }
  }

  .newsletter-sign-up-body {
    height: 7.375rem;
    overflow: hidden;
    margin-bottom: 20px;

    p {
      @include font(16);
    }

    @include media-breakpoint-down(sm) {
      padding: 20px 0;
      height: 9.375rem;
      overflow: hidden;
    }
  }

  .newsletter-read-more {
    padding-bottom: 20px;
  }

  .newsletter-signup-image-holder {
    img {
      width: 100%;
    }

    @include media-breakpoint-down(sm) {
      width: 40%;
      margin-right: 15px;
    }
  }

  .newsletter-signup-content-holder {
    padding-top: 28px;

    @include media-breakpoint-down(sm) {
      width: 60%;
      padding-top: 0;
    }

    h4 {
      @include font(16);
      font-weight: normal;
      font-style: normal;
      color: $color-grey-light;
      padding: 17px 0;

      @include media-breakpoint-down(sm) {
        padding: 0 45px 0 0;
      }
    }

    p {
      @include font(16);
      color: $color-grey;
      padding: 15px 0 0;
      letter-spacing: 1px;
      text-align: left;
    }

    .newsletter-signup-content-title {
      h3 {
        margin-bottom: 0;
        @include media-breakpoint-down(sm) {
          @include font(18);
        }
      }

      .add-newsletter-topic {
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
          width: initial;
        }
      }
    }

    .newsletter-tooltip {
      position: absolute;
      top: 30px;
      background-color: white;
      border-radius: 22px;
      height: 37px;
      z-index: 1000;
    }
  }
}

.tooltip {
  .tooltip-inner {
    background: #f4f5f7;
    color: #9e9e9e;
    border-radius: 22px;
    height: 38px;
    font-size: 16px;
    padding: 7px 21px;
  }

  &.bs-tooltip-left {
    .arrow {
      display: none;
    }
  }

  &.bs-tooltip-right {
    .arrow {
      display: none;
    }
  }

  &.bs-tooltip-top {
    .arrow {
      display: none;
    }
  }

  &.bs-tooltip-bottom {
    .arrow {
      display: none;
    }
  }
}

.heading-newsletter {
  margin-top: 40px;
  @include media-breakpoint-down(sm) {
    margin-top: 20px;
  }
}

.newsletter-signup-form {
  z-index: 1000;
  position: fixed;
  bottom: 0;
  right: 0;
  background-attachment: fixed;
  background-color: #efefef;

  .newsletter-signup-header {
    padding: 25px 25px 0 25px;
    margin: 0 -15px;
    display: flex;
    justify-content: space-between;

    @include media-breakpoint-down(sm) {
      padding: 20px 25px 0 25px;
    }

    &.header-background {
      background-color: $color-brand;
      padding-bottom: 25px;
    }

    .header-title {
      color: $color-white;
      visibility: visible;

      &.header-hide {
        visibility: hidden;
      }
    }

    .newsletter-btn {
      .fa-caret-up {
        color: $color-white;
      }

      .fa-caret-down {
        color: $color-grey-light;
      }
    }
  }

  .newsletter-signup-body {
    .msg {
      text-align: left;

      p {
        margin-bottom: 0;

        &.highlights {
          color: $color-brand;
          padding-left: 0.5rem;
        }

        &.bold {
          font-weight: 500;
        }

        &.msg-spacing {
          padding-bottom: 18px;
        }
      }

      .msg-body-subscribe {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .msg-body-response {
        display: none;
        padding-bottom: 45px;
        text-align: center;

        .cta-btn {
          background-color: $color-brand;
          padding: 2px 20px;
          border-radius: 18px;
          color: $color-white;
        }
      }

      .msg-body-addtag {
        //padding: 0 40px;
        display: none;

        .msg-body-addtag-header {
          padding: 0 40px;

          @include media-breakpoint-down(sm) {
            padding: 0 15px;
          }
        }

        .msg-body-addtag-body {
          padding: 0 40px;

          @include media-breakpoint-down(sm) {
            padding: 0 15px;
          }

          ul {
            padding-left: 0;
            list-style-type: none;
          }

          li {
            display: inline-block;
          }

          .tag {
            display: flex;//inline-block;
            align-items: inherit;
            padding: 9px 0;
            @include font(18);

            @include media-breakpoint-down(sm) {
              padding: 5px 0;
            }
          }

          .tag-divider {
            border-right: 1px solid $color-brand;
            margin-right: 10px;
            margin-left: 5px;
          }
        }
      }
    }

    .newsletter-subscribe {
      width: 100%;
      padding: 20px 0 30px;

      @include media-breakpoint-up(lg) {
        padding: 30px 5px 45px 25px;
      }

      p {
        @include font(16);
        color: $color-grey-light;

        &.email-reminder {
          padding-left: 10px;
        }

        &.warning {
          color: $color-brand;
          padding-left: 10px;
        }
      }

      .email-input {
        background: rgba(239, 239, 239, 0.3);
        border: 1px solid #cccccc;
        font-size: 18px;
        border-radius: 22px;
        width: 100%;
        height: 45px;
        padding: 10px 20px;

        @include media-breakpoint-up(lg) {
          border-top-left-radius: 22px;
          border-bottom-left-radius: 22px;
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
          width: 75%;
          max-width: 500px;
        }

        &::placeholder {
          color: #cdcdcd;
          opacity: 1;
        }

        &:focus {
          border-color: $color-brand;
        }

        caret-color: $color-brand;
      }

      .email-submit-btn {
        height: 45px;
        font-size: 18px;
        padding: 6px 46px;
        border-radius: 22px;
        background: $color-brand;
        color: $color-white;
        display: flex;
        align-items: center;
        margin: 20px auto 0;

        @include media-breakpoint-up(lg) {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border-top-right-radius: 22px;
          border-bottom-right-radius: 22px;
          width: 25%;
          margin-left: -1%;
          padding: 6px 0;
          display: inline-block;
        }
      }

      .email-terms {
        padding: 20px 0 0 10px;
      }
    }
  }
}

.highlights {
  color: $color-brand;
}

.newsletter-divider {
  margin: 0 15px;
  width: 100%;
  border-bottom: 1px solid $color-grey-lighter;

  &:nth-last-child(1) {
    border-bottom: none;
  }

  @include media-breakpoint-up(md) {
    display: none;
  }
}
// ==== newsletter sign up ends ===========
