@mixin set-cta-size($height, $border-w: null) {
  border-radius: $height / 2;
  height: $height;

  @if ($border-w != null) {
    @include font(18, $height - $border-w);
  }

  @else {
    @include font(18, $height);
  }
}

%cta,
.cta {
  $b-w: 1px;
  $b-w-medium: 2px;

  transition: color 0.3s $easeOutCubic, background-color 0.3s $easeOutCubic, box-shadow 0.2s $easeOutCubic;
  position: relative;
  cursor: pointer;
  display: inline-block;
  padding: 0 10px;
  outline: 0;
  background-color: transparent;
  letter-spacing: 1px;

  @include font-notosanssc-medium;
  @include set-cta-size($cta-h);

  &:focus {
    outline: 0;
  }

  &%cta-bordered,
  &.cta-bordered {
    border-style: solid;
    border-width: $b-w;

    @include set-cta-size($cta-h, $b-w * 2);

    &.cta-grey {
      border-color: $color-grey;
      color: $color-grey;

      &:hover {
        background-color: $color-grey;
        color: $color-white;
      }
    }

    &.cta-white {
      border-color: $color-white;
      color: $color-white;

      &:hover {
        background-color: $color-white;
        color: $color-brand;
      }
    }

    &%cta-red,
    &.cta-red {
      border-color: $color-brand;
      color: $color-brand;

      &:hover {
        background-color: $color-brand;
        color: $color-white;
      }
    }

    &.cta-red-white-bg {
      background-color: $color-white;
      border-color: $color-brand;
      color: $color-brand;
    }
  }

  &.cta-bordered-medium {
    border-width: $b-w-medium;

    @include set-cta-size($cta-h, $b-w-medium * 2);
  }

  &.cta-bordered-gradient {
    position: relative;

    span {
      position: relative;
      z-index: 1;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      visibility: visible;
      opacity: 1;
    }

    &::after {
      transition: opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic;
      background-clip: padding-box;
      border-style: solid;
      border-color: transparent;
      border-width: 1px;
    }

    &.cta-bordered-gradient-red {
      transition: color 0.4s $easeOutCubic;

      @include bordered-gradient(#f5515f, #9f041b, $color-white);

      color: $color-brand;

      &:hover {
        color: $color-white;

        &::after {
          visibility: hidden;
          opacity: 0;
        }
      }
    }
  }

  &.cta-bordered-gradient-medium {
    &::after {
      border-width: 2px;
    }
  }

  &.cta-solid {
    &.cta-gradient-red {
      background: linear-gradient(to bottom, $color-brand-light, $color-brand);
      color: $color-white;

      &:hover {
        box-shadow: 0 0 7px 0 rgba(75, 75, 75, 0.5);
      }
    }

    &.cta-solid-white {
      background-color: $color-white;
      color: $color-brand;
    }

    &.cta-solid-red {
      letter-spacing: 1px;
      background-color: $color-brand;
      color: $color-white;
      // padding: 0 20px;
      height: auto;

      &.add-padding {
        padding: 3px 46px;
        border-radius: 30px;
      }
    }

    &.cta-solid-grey {
      background-color: $color-grey;
      color: $color-white;
      padding: 0 20px;
      letter-spacing: 1px;
      height: auto;

      &.add-padding {
        padding: 3px 46px;
        border-radius: 30px;
      }
    }

    &.add-padding {
      padding: 3px 46px;
      border-radius: 30px;
    }
  }

  &.cta-play {
    position: relative;
    color: $color-white;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;

    > * {
      display: inline-block;
      vertical-align: top;
    }

    .svg-icon-play {
      height: 100%;
      max-width: 10px;
    }

    .cta-play-meta {
    }

    .cta-play-meta-text {

    }

    .cta-play-meta-category {
      @include font(14, $letterSpacing: $font-ls-regular);
    }

    .cta-divder {
      border-right: 1px solid $color-white;
      padding-right: 15px;
    }

    .cta-play-meta-divider {
      $m: 8px;

      width: 1px;
      height: calc(100% - #{($m * 2)});
      margin: $m ($m - 4px) $m ($m - 1px);
      background-color: $color-white;
    }

    .cta-play-meta-time {
      letter-spacing: normal;

      @include font(14);
    }

    &.cta-play-white {
      background-color: transparentize($color-white, 0.8);
    }

    &.cta-play-grey {
      background-color: $color-grey;
      padding: 4px 22px;
      height: auto;
      border-radius: 30px;
      margin: 0 auto;
      bottom: 26px;
      vertical-align: unset;

      .cta-circle {
        padding-left: 15px;
      }
    }

    &.cta-play-black {
      background-color: transparentize($color-black, 0.5);
    }

    &.cta-play-brand {
      background-color: $color-brand;
    }

    &.cta-play-circle {
      position: relative;
      border-radius: 50%;
      padding: 0;
      width: 30px;
      height: 30px;
      line-height: 30px;

      .svg-icon-play {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        max-width: 10px;
      }
    }

    &.cta-play-gradient {
      background: linear-gradient(to right, $color-blue-light, $color-blue);

      .svg-icon-play {
        max-width: none;
        margin-left: 8px;
        margin-right: 8px;
      }
    }
  }

  &.cta-maxw-fluid {
    width: 100%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;

    @include media-breakpoint-up(sm) {
      max-width: 170px;
    }
  }

  &.cta-minw {
    min-width: 80px;
    text-align: center;
  }

  &.cta-tall {
    @include set-cta-size($cta-h-tall);

    &.cta-bordered {
      @include set-cta-size($cta-h-tall, $b-w * 2);
    }
  }

  &:hover {
    text-decoration: none;
  }

  &.cta-small {
    padding: 0 8px;
    font-size: 12px;
    line-height: 18px;
  }

  &.cta-medium {
    padding: 2px 24px 4px;
    font-size: 16px;
    line-height: 24px;
  }

  &.cta-large {
    padding: 5px 24px 7px;
    font-size: 16px;
    line-height: 24px;
  }
}

textarea {
