}

.imaged {
  @include aspect-ratio(3, 2);

  position: relative;
  background-color: $color-white;

  img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }

  &.imaged-tn {
    width: auto;
    min-width: 100px;
    margin-right: 10px;
  }
}

.trigger {
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;

  &.trigger-start {
    background-color: green;
  }

  &.trigger-end {
    background-color: blue;
  }
}

