}

.svg {
  &.svg-icon-search {
    cursor: pointer;
  }

  &.svg-icon-play {
  }

  &.svg-icon-play-solid {

  }

  &.svg-icon-play-bordered {
    transition: transform 0.7s $easeOutExpo;
    cursor: pointer;
    transform: scale(1);

    path {
      transition: fill 0.4s $easeOutExpo;
      fill: transparent;
      opacity: 1;
    }

    &:hover {
      transform: scale(0.95);

      path {
        fill: $color-brand;
      }
    }
  }
}

