@mixin label-premium-regular {
  $h: 20px;

  @include font(13, $h, $font-ls-regular);

  border-radius: $h / 2;
  padding-left: 10px;
  padding-right: 10px;
  height: $h;
}

@mixin label-premium-small {
  $h: 15px;

  @include font(10, $h, $font-ls-regular);

  border-radius: $h / 2;
  padding-left: 8px;
  padding-right: 6px; // agaration visually
  height: $h - 1px; // agaration visually

  @include media-breakpoint-up(lg) {
    height: 16px; // agaration visually
  }
}

.search-query {
  background-color: $color-whitepale-light;
  padding-top: 100px;
  padding-bottom: 100px;

  .searchbar {
    position: relative;
  }

  .svg-icon-search {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);

    path {
      fill: $color-grey;
    }
  }

  input {
    &[type='search'] {
      background-color: transparent;
      width: 100%;
      padding-left: 30px;

      &::placeholder {
        color: $color-grey-lighter;
      }

      &:focus {
        border-bottom: 1px solid $color-brand;
      }
    }
  }
}

.search-query-result-header {
  padding-top: 30px;

  @include media-breakpoint-up (md) {
    padding-top: 60px;
  }

  h1 {
    font-weight: 500;
    color: $color-grey;
    @include font(24);

    @include media-breakpoint-up (md) {
      @include font(36);
    }

    span {
      color: $color-brand;
    }
  }
}

.search-results {
  .nav-tabs {
    border: 0;

    .nav-tab {
      position: relative;

      &:first-child {
        &::after {
          content: "";
          margin: auto;
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          width: 1px;
          height: 50%;
          background-color: $color-grey-lighter;
        }
      }

      .nav-link {
        border: none;
        color: #9e9e9e;
        @include font (14);

        &.active {
          color: $color-grey;
        }
      }
    }
  }

  .heading-border {
    &.heading-border-secondary {
      h2 {
        @include font (14);
        padding: 0;
      }
    }
  }

  .tab-content {
    padding-top: 30px;

    .tab-pane {
      ol {
        list-style-type: none;
        padding-left: 0;

        .single-search-result {
          padding-bottom: 20px;
          margin: 0 0 20px;
          border-bottom: 1px solid $color-grey-lighter;

          &:last-child {
            border-bottom: none;
          }

          .search-thumbnail {
            padding-left: 15px;

            @include media-breakpoint-up (md) {
              padding-left: 0;
            }

            img {
              width: 100%;
            }
          }

          .search-content {
            padding-top: 20px;

            @include media-breakpoint-up (md) {
              padding-top: 0;
            }

            h3 {
              font-weight: bold;
              @include font(18);
              @include media-breakpoint-up (md) {
                @include font(18);
              }
            }

            .search-blurb {
              margin-top: 15px;
              color: #6f6f6f;
              @include font (16);
            }

            .search-info {
              @include font(12);
              color: #6f6f6f;
              margin-top: 10px;
            }
          }
        }
      }
    }
  }
}

