.label {
  display: inline-block;
  margin-bottom: 8px;
  margin-right: 10px; //for more spaceing

  &.label-premium {
    // 
    background-color: $color-pink;
    color: $color-white;

    @include font-notosanssc-bold;

    &.regular {
      @include label-premium-regular;
    }

    &.small {
      @include label-premium-small;
    }
  }

  &.label-avt {
    // advertisement
    color: $color-grey-light;
    border: solid 1px $color-grey-light;

    @include font(10);

    padding: 0 8px;
    border-radius: 10px;
  }

  &.label-topic {
    $h: 20px;

    background-color: $color-whitepale-light;
    color: $color-brand;
    margin-bottom: 8px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 5px;
    }

    @include font(14, $h);
    font-weight: 500;
    height: $h;
    border-radius: $h / 2;
    padding: 0 10px;

    &:hover {
      text-decoration: none;
    }
  }

  &.label-category {
    $h: 26px;

    background-color: $color-whitepale-light;
    color: $color-grey-light;

    @include font(16, $h - 2px); // visually align

    height: $h;
    border-radius: $h / 2;
    padding: 0 18px;
    margin: 5px 0;

    &:hover {
      text-decoration: none;
    }

    &.is-active {
      background-color: $color-brand;
      color: $color-white;
    }
  }

  &.label-common {
    $h: 26px;
    background-color: $color-whitepale;
    color: $color-grey;
    @include label-premium-regular;
    border-radius: $h / 2;
    padding: 0 10px;
  }

  &.label-exclusive {
    $h: 26px;
    background-color: $color-grey;
    color: $color-white;
    @include label-premium-regular;
  }

  &.label-sponsor {
    $h: 20px;
    background-color: $color-whitepale-light;
    color: $color-grey-light;
    @include font(13, $h);
    font-weight: 500;
    height: $h;
    border-radius: $h / 2;
    padding: 0 10px;
    margin-bottom: 8px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 5px;
    }
  }
}

