// =====================================================================
// Content
// =====================================================================

.content {

}

.content-primary {
  .page-article & {
    max-width: 100%;

    @include media-breakpoint-up(lg) {
      max-width: 500px;
    }

    @include media-breakpoint-up(xl) {
      max-width: 100%;
    }

    @include media-breakpoint-up(xxl) {
      max-width: 860px;
    }

    // .label-topic {
    //     // margin-bottom: 20px;
    // }
  }

  // drupal output html outside of .article-content
  figure {
    img {
      max-width: 100%;
    }

    figcaption {
      text-align: left;
      color: $color-grey-light;
      padding: 16px 0;
      margin-top: l-em($font-base-size, 0);
      margin-bottom: l-em($font-base-size, 0);

      @include font(14, 1.4, $isEm: true);

      @include media-breakpoint-up(lg) {
        @include font(14, 1.5, $isEm: true);
      }
    }

    &.image-placeholder { // legacy css class
      text-align: center;
    }
  }

  // all fontsizing related and spacing em styles
  .article-content-rawhtml {
    @include font($font-base-size, 1.94, 0.5px, $isEm: true);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      @include font-notosanssc-medium;
    }

    h1 {
      @include font(11, 1.94, $isEm: true);
    }

    h3 {
      @include font(22, 1.5);

      a {
        color: $color-blue-900;
        font-size: inherit;
        border-bottom: 1px solid $color-blue-900;

        &:hover {
          text-decoration: none;;
        }
      }
    }

    h2,
    h4,
    h5,
    h6 {
      @include font(10, 1.94, $isEm: true);

      a {
        color: $color-blue-900;
        font-size: inherit;
        border-bottom: 1px solid $color-blue-900;

        &:hover {
          text-decoration: none;;
        }
      }
    }

    p {
      font-size: em(10); // unset compounding
      // margin-top: l-em($font-base-size, 30);
      margin-bottom: l-em($font-base-size, 30);

      a {
        @include font(inherit, $isEm: true);
      }
    }

    figure {
      margin-bottom: l-em($font-base-size, 40);

      &.inline-figure-l {
        @include media-breakpoint-up (lg) {
          margin-right: l-em($font-base-size, 24);
          margin-bottom: l-em($font-base-size, 24);
        }
      }

      &.inline-figure-r {

        @include media-breakpoint-up (lg) {
          margin-left: l-em($font-base-size, 24);
          margin-bottom: l-em($font-base-size, 24);
        }
      }

      figcaption {
        @include font(14); // unset compounding, agar to 13
        font-weight: normal;
        // @include media-breakpoint-up(lg) {
        //   @include font(14); // unset compounding, agar to 14
        // }
      }
    }

    .inline-blockquote {
      @include font(24); // unset compounding, agar to 20
    }

    .inline-profile {
      a {
        @include font(10, $isEm: true); // unset compounding, agar to 18
      }
    }

    .inline-recommend {

      .section-header {
        padding: 0 16px;
        &::before {
          display: none;
        }

        .section-header-title {
          h2 {
            @include font(12.2223, $isEm: true)
          }
        }
      }

      p {
        margin-top: 0;
      }
    }

    .avt {
      border-top: 1px solid $color-whitepale;
      margin-bottom: 24px;
      padding: 8px;
      // margin-top: l-em($font-base-size, 40);
      // margin-bottom: l-em($font-base-size, 40);
    }
  }

  .article-label-container {
    padding-top: 14px;
    display: block;

    @include media-breakpoint-up (lg) {
      display: none;
      // max-width: 25%;
      // flex-direction: row;
      // flex-wrap: wrap;
      // position: absolute;
      // left: calc(-25% - 16px);
    }
  }
}
