/* lottey styling starts here */
.disclaimer-line {
  border-top: 1px solid $color-grey-lighter;
  border-bottom: 1px solid $color-grey-lighter;
  padding: 15px 0;
}

.disclaimer-note {
  // width: 70%;
  margin: 1em auto;
  background: $color-whitepale-light;
  padding: 2em 4em;
}

.lottery-header-container {
  text-align: center;

  h1,
  h2,
  h3 {
    &.red {
      color: $color-brand;
    }
  }
}

.lottery-container {
  width: 100%;
  height: fit-content;
  padding: 2% 0 0;
  border: 3px solid $color-black;
  margin: 20px auto;

  &.bigsweep {
    width: 100%;

    @include media-breakpoint-up(md) {
      width: 90%;
    }

    @include media-breakpoint-up(lg) {
      width: 75%;
    }

    @include media-breakpoint-up(xl) {
      width: 60%;
    }
  }

  &.toto {
    padding: 0;

    @include media-breakpoint-up(md) {
      width: 90%;
    }

    @include media-breakpoint-up(lg) {
      width: 75%;
    }

    @include media-breakpoint-up(xl) {
      width: 60%;
    }
  }

  @include media-breakpoint-up(md) {
    width: 60%;
  }

  @include media-breakpoint-up(lg) {
    width: 50%;
  }

  @include media-breakpoint-up(xl) {
    width: 45%;
  }

  .lottery-header {
    text-align: center;

    h3 {
      text-align: center;
      @include font(20);
      line-height: normal;
      color: $color-brand;
      @include media-breakpoint-up(md) {
        @include font(25);
      }
    }

    h2 {
      font-family: serif;
      @include font(28);
      @include media-breakpoint-up(md) {
        @include font(32);
      }
    }

    h1 {
      font-weight: 900;
      @include font(30);
      @include media-breakpoint-up(md) {
        @include font(40);
      }
    }

    &.toto-header {
      background: $color-brand;

      h3 {
        color: $color-white;
      }
    }
  }

  .lottery-logo-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .toto-logo {
      height: 30px;
      @include media-breakpoint-up(sm) {
        height: 49px;
      }
    }

    .headliner {
      margin: 0 auto;

      p {
        @include font(18);
        margin: 0;
        line-height: normal;
        @include media-breakpoint-up(sm) {
          @include font(20);
        }
      }

      h3 {
        @include font(18);
        margin: 0;
        line-height: normal;

        @include media-breakpoint-up(sm) {
          @include font(24);
        }
      }
    }
  }

  .lottery-result {
    width: 100%;
    text-align: center;
    padding-top: 10px;
    // display: flex;
    // justify-content: center;
    // flex-direction: row;
    .underline {
      text-decoration: underline;
    }

    .toto-result {
      display: flex;
      justify-content: space-around;
      align-items: center;

      h3 {
        @include font(24);
      }

      &.special-num {
        border-left: 2px dashed $color-grey-lighter;
      }
    }

    .toto-result-title {
      background: $color-brand;
      padding: 5px 0;
      // margin: 0 -16px;
      h4 {
        color: $color-white;
        @include font(18);
        @include media-breakpoint-up(md) {
          @include font(20);
        }
      }
    }

    .big-sweep-spacing {
      // padding-left: 10px;
      @include font(26);
      width: 46%;
      text-align: right;
      @include media-breakpoint-up(md) {
        width: 38%;
        @include font(30);
      }
    }

    h1 {
      font-weight: 900;
      @include font(30);
      @include media-breakpoint-up(md) {
        @include font(38);
      }
    }

    h2 {
      @include font(29);
      line-height: 1.2;
      @include media-breakpoint-up(md) {
        @include font(34);
      }

      &.bigsweep-header {
        @include font (26);
        text-align: left;
        // justify-content: space-evenly;
        @include media-breakpoint-up(md) {
          @include font (30);
        }
      }
    }

    h3 {
      @include font(26);

      &.prize-header {
        text-align: center;
        // padding: 5px 0 5px 20px;
        color: $color-brand;
      }

      &.smaller-header {
        @include font(20);
        @include media-breakpoint-up(md) {
          @include font(24);
        }
      }
    }

    .big-prize {
      padding-bottom: 20px;

      .big-prize-container {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: baseline;

        .big-prize-group {
          width: 28%;
          text-align: center;

          span {
            @include font (26);
            @include media-breakpoint-up(md) {
              @include font (30);
            }
          }
        }
      }
    }

    .prize-subheader {
      background: $color-whitepale;
      padding: 0.5% 0;
    }

    .prize-container {
      .prize-result {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 1% 1.5%;
        justify-content: center;

        &.justify-left {
          justify-content: left;
        }

        &.delight-prize-result {
          justify-content: center;

          p {
            @include font(24);
            padding: 0 1%;
          }
        }

        .start-prize-group {
          @include font(24);
          width: 50%;
          // padding: 0 10px;
          @include media-breakpoint-up(md) {
            width: 20%;
            @include font(24);
          }

          span {
            width: 50%;

            &.add-padding {
              padding: 0 2%;
            }
          }
        }

        h2 {
          width: 50%;
          display: block;
          text-align: center;
          color: $color-black;
          @include media-breakpoint-up(sm) {
            width: 33%;
          }
        }
      }
    }
  }

  .lottery-disclaimer {
    border-top: 1px solid $color-grey-lighter;
    border-spacing: 2px;
    padding: 2% 3% 0;

    h4 {
      font-weight: bold;
    }

    a {
      @include font(15);
      color: $color-brand;

      &:active {
        color: $color-brand;
      }

      &:hover {
        color: $color-grey-lighter;
      }
    }

    p {
      @include font(14);
    }

    &.toto-disclaimer {
      text-align: center;
    }
  }

  .table {
    width: 100.1%;

    .thead-dark {
      th {
        background: $color-brand;
        border: none;
      }
    }
  }
}

/* lottey styling ends here */
