// =====================================================================
// Header (header)
// =====================================================================

.header {
  position: relative;
  z-index: 3; //9;

  &.header-mobile {
    display: block;
    border-bottom: 1px solid $color-grey-light;

    @include media-breakpoint-up(lg) {
      display: none;
    }

    .user-not-login {
      display: block;
    }

    .user-login {
      display: none;
    }

    .registered-user {
      display: none;
    }
  }

  &.header-desktop {
    display: none;

    @include media-breakpoint-up(lg) {
      display: block;
    }

    &.is-sticky {
      animation-name: fade-in-down;
      animation-duration: 500ms;
      position: sticky;
      top: 0;
    }
  }

  &.js-is-sticky,
  &.js-is-stuck {
    // START - to fix lazyload images from giving sitckybits wrong values
    position: sticky;// stylelint-disable-line declaration-no-important
    bottom: auto !important; // stylelint-disable-line declaration-no-important
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    // END - to fix lazyload images from giving sitckybits wrong values

    .is-scrolled-down & {
      .topnavbar-secondary {
        .cta-holder {
          display: inline-block;
        }

        .logo {
          display: block;
        }
      }

      .topnavbar-mobile {
        .logotype {
          display: flex;
          transform: translate3d(-150px, -50%, 0);
          visibility: hidden;
          opacity: 0;

          @include media-breakpoint-up(sm) {
            transform: translate3d(-210px, -50%, 0);
            opacity: 0;
          }

          @include media-breakpoint-up(md) {
            transform: translate3d(-290px, -50%, 0);
            opacity: 0;
          }

          @include media-breakpoint-down(xs) {
            // opacity: 0;
            // visibility: hidden;
          }

          .svg-logotype-type {
            transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
            visibility: hidden;
            opacity: 0;
          }
        }

        .mobile-logotype {
          display: inline-flex;
        }

        .mobile-text-links {
          transition: visibility 0.4s 0.3s $easeOutCubic, opacity 0.4s 0.3s $easeOutCubic;
          visibility: visible;
          opacity: 1;
        }

        // .social-dropdown,
        // .fontsizing-dropdown {
        //   display: flex;
        // }
      }

      .newsticker {
        transform: translateY(-100%);
      }

      .col-animate {
        margin-right: 22px;
        // @include make-col(1);
      }
    }

    .is-scrolled-up & {
    }

    ~ .js-stickybits-usefixed-spacer {
      display: block;
    }
  }
}
