// =====================================================================
// Topnavbar
// =====================================================================

.topnavbar {
  position: relative;

  ul {
    @include reset-list;
  }

  a {
    &:hover {
      text-decoration: none;
    }

    &.cta {
      // letter-spacing: 1px;
      // padding: 0 8px;
      // @include font (13);
      // line-height: 28px;
      margin-left: 10px;
    }
  }

  .ham {
    padding-left: 0;
    padding-right: 0;
    display: inline-block;
    // margin-right: 15px;
    padding-top: 10px;
    margin-left: 5px;
    outline: none;

    @include media-breakpoint-up(lg) {
      display: none;
    }

    &[aria-expanded='true'] {
      .hamburger {
        > div {
          transition: background-color 0.4s $easeInCubic, transform 0.4s $easeInCubic;
          background-color: transparent;

          &:nth-child(1) {
            // transform: translateY($ham-h / 2);
          }

          &:nth-child(2) {
            &::before,
            &::after {
              transition: background-color 0.4s 0.3s $easeOutCubic, transform 0.4s 0.3s $easeOutCubic;
              background-color: $color-grey-light;
            }

            &::before {
              transform: rotate(45deg);
            }

            &::after {
              transform: rotate(-45deg);
            }
          }

          &:nth-child(3) {
            // transform: translateY(-$ham-h / 2);
          }
        }
      }
    }
  }

  .hamburger {
    @include hamburger($ham-w, $ham-h, 3px, $color-white);

    > div {
      transition: background-color 0.4s 0.3s $easeOutCubic, transform 0.4s 0.3s $easeOutCubic;
      transform: translateY(0);

      &:nth-child(2) {
        &::before,
        &::after {
          transition: background-color 0.4s 0.7s $easeOutCubic, transform 0.4s $easeOutCubic;
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 1.5px;
          background-color: transparent;
        }
      }
    }
  }

  .topnav-submenu {
    position: relative;
    column-rule: solid 1px transparentize($color-grey-light, 0.5);
    font-size: 0;
    padding: 10px 0;
    column-count: 2;

    @include media-breakpoint-up(lg) {
      box-shadow: 0 4px 6px 0 rgba(157, 157, 157, 0.3);
    }

    > a {
      @include font-notosanssc-regular;
    }
  }

  .dropdown {
    .no-touchevents & {
      &:hover {
        .dropdown-toggle {
          background-color: $color-whitepale-light;
          box-shadow: 0 4px 6px 0 rgba(157, 157, 157, 0.3);
          color: $color-brand;
        }

        .dropdown-menu {
          @include media-breakpoint-up(lg) {
            // background-color: red;
            // to display dropdown
            // display: block;
            // left: unset;
            // right: 0;
          }
        }
      }
    }

    .dropdown-toggle {
      &:active {
        @include media-breakpoint-up(lg) {
          pointer-events: none;
        }
      }
    }

    &.show {
      @include media-breakpoint-up(lg) {
        background-color: transparent;
      }
    }

    &.user-dropdown {
      .no-touchevents & {
        &:hover {
          .dropdown-toggle {
            background-color: transparent;
            box-shadow: none;
          }
        }
      }
    }
  }

  .dropdown-menu {
    background-color: #f9f9f9;
    border: 0;
    border-radius: 0;
    margin: 0;
    padding: 0;
    width: 100%;

    @include media-breakpoint-down(md) {
      position: relative; // !important; // stylelint-disable-line declaration-no-important
      transform: none !important; // stylelint-disable-line declaration-no-important
    }

    @include media-breakpoint-up(lg) {
      width: auto;
    }

    .topnav-link {
      color: $color-grey;
      padding: 5px #{($grid-gutter-width / 2)};

      @include media-breakpoint-up(lg) {
        padding: 5px #{($grid-gutter-width / 2)};
      }
    }
  }

  .dropdown-item {
    &:hover {
      background-color: transparent;
      color: $color-brand;
    }
  }

  .cta-holder {
    vertical-align: middle;
    overflow: hidden;
    white-space: nowrap;

    .cta {
      &:first-of-type {
        margin-left: 0;
      }
    }
  }
}

