.topnav-link {
  // transition: color 0.3s $easeOutCubic;
  display: inline-block;
  color: $content-inactive-inverse; //$color-white;

  @include font-notosanssc-medium;
  @extend %l-topnav-link-padding-x;
  letter-spacing: 2px;

  &.is-active {
    .topnavbar-primary & {
      background-color: $color-whitepale-light;
      color: $color-brand;
      height: $cta-h;
      border-radius: $cta-h / 2;
    }

    .topnavbar-secondary & {
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        background-color: $color-whitepale-light;
        width: calc(100% - #{$grid-gutter-width});
        height: 4px;
      }
    }

    &.dropdown-item {
      background-color: transparent;
      color: $color-grey;

      .topnavbar-secondary &,
      .topnavbar-mobile & {
        color: $color-brand;
      }
    }
  }

  &.is-promoted {
    .topnavbar-primary & {
      @include font-notosanssc-bold;
      color: $color-brand;
    }
  }
}
