.author-byline-container {
  background-color: $color-whitepale-light;
  padding: 20px 30px;
  margin-top: -30px;
  margin-left: -15px;
  margin-right: -15px;

  @include media-breakpoint-up (sm) {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
  }

  @include media-breakpoint-up (md) {
    margin-top: 20px;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: auto auto;
  }

  .author-profile {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .author-profile-shot {
      min-width: 60px;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: $color-whitepale;
      background-image: url('./../images/icon/avatar-holder.svg');
      background-position: center;
      background-size: cover;
      box-shadow: 0 0 10px rgba(103, 103, 103, 0.5);
      background-repeat: no-repeat;
      @include media-breakpoint-up (md) {
        min-width: 100px;
        width: 100px;
        height: 100px;
      }
    }

    .author-byline {
      align-self: center;
      padding: 0 20px;
      border: none;

      @include media-breakpoint-up (md) {
        border-right: 1px solid $color-whitepale;
      }
      width: 100%;
    }
  }

  h2 {
    @include font(24);
    font-weight: 500;
    color: $color-brand;
  }

  p {
    margin-bottom: 0;
  }

  a {
    &:hover {
      text-decoration: none;
    }
  }

  .author-description {
    align-self: center;
    // width: 100%;
    padding-left: 0;
    @include media-breakpoint-up (md) {
      padding-left: 20px;
    }

    .author-intro {
      margin-top: 15px;
      @include media-breakpoint-up (md) {
        margin-top: 0;
      }
    }
  }
}
