// =====================================================================
// Breadcrumbs
// =====================================================================

.breadcrumbs {

}

.breadcrumbs-list {
  @include reset-list;

  font-size: 0;

  li {
    position: relative;
    display: inline-block;
    padding: 0 10px;

    &::after {
      content: '';
      width: 1px;
      height: 60%;
      background-color: $color-grey-light;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      @include font-notosanssc-bold;

      padding-right: 0;
      border: 0;

      &::after {
        display: none;
      }

      a {
        color: $color-grey;
      }
    }

    a {
      @include font(14);

      color: $color-grey-light;
    }
  }
}
