// =====================================================================
// Home
// =====================================================================

.page-home {
  #app-cta {
    display: none;
  }

  .premium-notifier {
    // display: block;

    .premium-notifier-modal {
      max-width: 720px;

      .premium-notifier-content {
        background-color: #f4f5f7;
        border: none;
        border-radius: 0;
        height: auto;
        padding: 60px;
        box-shadow: 0 0 10px rgba(58, 58, 58, 0.5);

        .premium-notifier-header {
          display: flex;
          justify-content: space-between;
          border: none;

          img {
            width: 100%;
          }
        }

        .premium-notifier-body {
          position: relative;
          flex: 1 1 auto;
          padding: 35px 0 30px;

          p {
            line-height: 26px;
            padding-bottom: 1rem;
          }
        }

        .premium-notifier-footer {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: center;
          border: none;

          a {
            color: $color-brand;

            &:hover {
              text-decoration: none;
            }

            .underline {
              margin: 0 auto;
              width: 35px;
              height: 3px;
              background-color: $color-brand;
            }
          }
        }
      }
    }
  }

  .js-is-sticky {
    .topnavbar-mobile {
      .fontsizing-dropdown {
        display: none;
      }

      .social-dropdown {
        display: none;
      }
    }
  }
}
