// =====================================================================
// Listing
// =====================================================================

.page-listing {
  .category-filter-list {
    @include reset-list;

    font-size: 0;
    margin-bottom: 20px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }

    li {
      display: inline-block;
      margin-right: 15px;
    }
  }

  .finance-stock {
    background-color: $color-whitepale-light;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: 20px;
    margin-bottom: 40px;

    iframe {
      width: 100%;
      margin-bottom: 20px;

      @include media-breakpoint-up(lg) {
        margin-bottom: 0;
      }
    }
  }

  .js-is-sticky {
    .topnavbar-mobile {
      .fontsizing-dropdown {
        display: none;
      }

      .social-dropdown {
        display: none;
      }
    }
  }
}
