.avt {
  border-radius: 0;
  text-align: center;
  // background-color: $color-whitepale-light;
  padding: 12px;

  .aside-primary & {
    // padding: 16px 0;
    margin-left: auto;
    margin-right: auto;
    // border-radius: 8px; // newly added
  }

  .avt-label {
    @include font(12, $letterSpacing: $font-ls-small);

    color: $color-grey-light;
    margin-bottom: 8px;
  }

  &.avt-mb {
    margin-bottom: 0;

    @include media-breakpoint-up(lg) {
      margin-bottom: 30px;
    }
  }
}

.advertisement {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;

  img {
    width: 100%;
    object-fit: contain;
    object-position: center;
  }

  &::before,
  &::after {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    white-space: pre-wrap;
  }

  &::before {
    top: 4px;
  }

  &::after {
    bottom: 4px;
  }
}

