@keyframes horoscope {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 0 -960px;
  }
}

@keyframes fade-out-up {
  0% {
    visibility: visible;
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    visibility: hidden;
    transform: translateY(-100%);
    opacity: 0;
  }
}

@keyframes fade-in-up {
  0% {
    visibility: hidden;
    transform: translateY(100%);
    opacity: 0;
  }

  100% {
    visibility: visible;
    transform: translateY(0);
    opacity: 1;
  }
}
