// toggle button for language

.switch-button {
  background: $color-white;
  border-radius: 30px;
  font-weight: bold;
  overflow: hidden;
  width: 56px;
  text-align: center;
  font-size: 13px;
  letter-spacing: 1px;
  position: relative;
  padding-right: 42px;
  margin-right: 20px;
  display: inline-block;
  border: 1px solid #cccccc;
  height: 30px;

  .switch-button-checkbox {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 2;

    + {
      .switch-button-label {
        position: relative;
        padding: 4px 0;
        display: block;
        user-select: none;
        left: 0;
        width: 28px;
        pointer-events: none;

        &::before {
          content: "";
          background: $color-whitepale-light;
          height: 28px;
          width: 28px;
          position: absolute;
          left: 0;
          top: 0;
          border-radius: 30px;
          transform: translateX(0);
          transition: transform 100ms;
        }

        .switch-button-label-span {
          position: relative;
          top: 0;
          color: $color-grey;
        }

        .switch-to-lang-tw {
          position: absolute;
          top: 0;
          bottom: 0;
          right: -28px;
          width: 28px;
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 3;
          pointer-events: none;
          color: $color-grey-light;
        }
      }
    }

    &:checked {
      + {
        .switch-button-label {
          &::before {
            transform: translateX(26px);
            transition: transform 100ms linear;
          }

          .switch-button-label-span {
            color: $color-grey-light;
          }

          .switch-to-lang-tw {
            color: $color-grey;
          }
        }
      }
    }
  }
}
