// =====================================================================
// Mobile Drawer
// =====================================================================

.mobile-drawer-bg {
  transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color-black, 0.5);
  visibility: hidden;
  opacity: 0;

  &.is-active {
    visibility: visible;
    opacity: 1;
  }
}

.mobile-drawer {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 9;
  width: 100%;
  transform: translateY(100%);
  display: block;

  @include media-breakpoint-up(lg) {
    display: none;
  }

  &.is-active {
    .mobile-drawer-arrow {
      opacity: 1;
      visibility: visible;
    }

    .mobile-drawer-handle {
      transform: translateY(100%);
      opacity: 0;
      visibility: hidden;
    }
  }

  &.is-scrolled {
    .mobile-drawer-content {
      .aside-set {
        height: 70vh;
      }
    }
  }

  .ps {
    &:hover {
      > .ps__rail-x,
      > .ps__rail-y {
        opacity: 0;
      }
    }

    .ps__rail-x,
    .ps__rail-y {
      &:hover {
        opacity: 0;
      }

      &:focus {
        opacity: 0;
      }

      &.ps--clicking {
        opacity: 0;
      }
    }
  }

  .ps--focus {
    > .ps__rail-x,
    > .ps__rail-y {
      opacity: 0;
    }
  }

  .ps--scrolling-x {
    > .ps__rail-x {
      opacity: 0;
    }
  }

  .ps--scrolling-y {
    > .ps__rail-y {
      opacity: 0;
    }
  }
}

.mobile-drawer-cancel {
  $btn-w: 15px;

  transition: transform 0.4s 0.3s $easeOutCubic;

  @include btn-cancel($btn-w, 100%, 1px, $color-brand);

  transform: scale(1);
  margin: auto;
}

.mobile-drawer-content {
  position: relative;
  z-index: 9;

  .aside-set {
    position: relative;
    transition: height 0.5s $easeInOutCubic;
    height: 50vh; // to tally with js
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    border-radius: $br-regular;
    background-color: $color-whitepale-light;

    // backface-visibility: hidden;
    // transform-style: preserve-3d;
    // will-change: transform;
    // transform: translateZ(0);
  }
}

.mobile-drawer-handle {
  transition: transform 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic;
  position: absolute;
  z-index: 9;
  top: -$mobile-aside-handle-h + 1px;
  left: 0;
  right: 0;
  width: calc(100% - 16px);
  margin: auto;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  height: $mobile-aside-handle-h;
  background-color: $color-whitepale-light;
  box-shadow: 0 -2px 3px 0 rgba($color-grey-light, 0.5);

  .handle-indicator {
    background-color: $color-grey-lighter;
    margin: 6px auto 2px;
    width: 30px;
    height: 4px;
    border-radius: 2px;
  }

  h2 {
    text-align: center;

    @include font(13);

    color: $color-brand;
    margin-bottom: 0;
    max-width: 34px;
  }

  .divider {
    position: absolute;
    top: 0;
    right: 0;
    width: 1px;
    height: 100%;
    background-color: $color-grey-lighter;
  }

  .mobile-drawer-text {
    display: flex;
    flex-direction: row;
    align-items: center;

    .cta-play {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }

  .mobile-drawer-text-link {
    @include font(14, $letterSpacing: $font-ls-small);
    @include truncate;

    flex: 0 1 auto;
  }

  &.is-hidden,
  &.is-hidden-perma {
    transform: translateY(100%);

    .mobile-drawer-cancel {
      transition: transform 0.4s 0.1s $easeOutCubic;
      transform: scale(0);
    }
  }
}

.mobile-drawer-arrow {
  transition: opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic;
  position: absolute;
  width: 100%;
  height: $mobile-aside-handle-h;
  top: -$mobile-aside-handle-h;
  opacity: 0;
  visibility: hidden;

  .line {
    $line-w: 20px;

    position: absolute;
    width: $line-w;
    height: 2px;
    background-color: $color-white;
    border-radius: 1px;
    top: 50%;

    &:first-of-type {
      left: calc(50% - #{$line-w / 2} - 9px);
      transform: rotate(20deg);
    }

    &:last-of-type {
      left: calc(50% - #{$line-w / 2} + 9px);
      transform: rotate(-20deg);
    }
  }
}

.aside-mobile {
  .aside-set {
    margin-left: 4px;
    margin-bottom: 4px;
    // box-shadow: 0 0 4px 0 rgba($color-whitepale-light, 0.5);
  }

  .aside-item {
    margin-bottom: 0;
  }

  .slick-list {
    padding-right: 20px;
  }
}
