@import url('https://fonts.googleapis.com/css?family=Noto+Sans+SC:100,300,400,500,700');

@mixin font-notsanssc-thin {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 100;
}

@mixin font-notsanssc-light {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 300;
}

@mixin font-notosanssc-regular {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 400;
}

@mixin font-notosanssc-medium {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 500;
}

@mixin font-notosanssc-bold {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 700;
}

@mixin font-notosanssc-black {
  font-family: 'Noto Sans SC', Arial, sans-serif;
  font-weight: 900;
}
