// =====================================================================
// Overlay
// =====================================================================

.overlay {
  transition: visibility 0.5s $easeOutCubic, opacity 0.5s $easeOutCubic;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  visibility: hidden;
  opacity: 0;

  &.is-active {
    z-index: 999;
    visibility: visible;
    opacity: 1;

    .overlay-wrap {
      transform: scale(1);
    }
  }
}

.overlay-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &.overlay-bg-grey {
    background-color: rgba($color-black, 0.6);
  }
}

.overlay-wrap {
  transition: transform 0.5s $easeOutCubic;
  position: relative;
  transform: scale(0.9);
  margin-left: auto;
  margin-right: auto;
  background-color: $color-white;
  box-shadow: 0 0 9px 0 rgba(49, 49, 49, 0.5);
  max-width: calc(100vw - #{$grid-gutter-width * 2});

  @include media-breakpoint-up(lg) {
    max-width: 880px;
  }

  .cancel {
    transition: background-color 0.4s $easeOutCubic;
    $w: 36px;

    position: absolute;
    z-index: 2;
    top: -($w / 2);
    right: -($w / 2);
    width: $w;
    height: $w;
    cursor: pointer;
    border-radius: 50%;
    border: solid 2px $color-white;
    background-color: transparentize($color-grey, 0.5);

    &:hover {
      background-color: transparentize($color-grey, 0.25);

      &::before {
        transform: rotate(45deg) scale(0.9);
      }

      &::after {
        transform: rotate(-45deg) scale(0.9);
      }
    }

    &::before,
    &::after {
      transition: transform 0.4s $easeOutCubic;
      content: '';
      width: 2px;
      height: 20px;
      background-color: $color-white;
      position: absolute;
      top: calc(50% - 10px);
      left: calc(50% - 1px);
    }

    &::before {
      transform: rotate(45deg) scale(1);
    }

    &::after {
      transform: rotate(-45deg) scale(1);
    }
  }
}

.overlay-content {
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  max-height: calc(100vh - 60px);

  @include media-breakpoint-up(lg) {
    max-height: 100%;
  }

  &.overlay-content-search {

  }

  .overlay-content-search-query {

  }

  .overlay-content-search-keywords {

  }
}

.overlay-microtransaction {
  .overlay-content {
    padding-top: 30px;

    @include media-breakpoint-up(sm) {
      padding-top: 60px;
    }
  }

  .label {
    &.label-premium {
      vertical-align: top;

      @include label-premium-small;

      margin-top: 3px;

      @include media-breakpoint-up(sm) {
        @include label-premium-regular;

        margin-top: 5px;
      }
    }
  }

  .microtransaction-header {
    text-align: center;

    p {
      @include font(15, $letterSpacing: $font-ls-small);

      margin-bottom: 4px;

      @include media-breakpoint-up(sm) {
        @include font(16, $letterSpacing: $font-ls-small);
      }
    }
  }

  .microtransaction-body {
    margin-top: 40px;
    margin-bottom: 40px;
    text-align: left;

    @include media-breakpoint-up(sm) {
      margin-top: 60px;
      margin-bottom: 60px;
      text-align: center;
    }

    @include media-breakpoint-up(lg) {
      text-align: left;
    }

    > .row {
      > [class*='col-'] {
        @include media-breakpoint-up(lg) {
          border-right: 1px solid $color-whitepale;

          &:last-of-type {
            border-right: 0;
          }
        }
      }
    }

    .cta {
      margin-bottom: 8px;

      @include media-breakpoint-up(lg) {
        margin-bottom: 20px;
      }
    }
  }

  .microtransaction-option {
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 30px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }

    p {
      color: $color-grey-light;

      @include font(14);
    }
  }

  .svg-icon-play-solid {
    max-width: 20px;
  }

  .icon-unlock {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(calc(50% - 10px), calc(-100% + 15px));
    max-width: 40px;

    @include media-breakpoint-up(lg) {
      max-width: $icon-unlock-maxw;
    }
  }

  .avt {
    margin-bottom: 0;
  }
}

.overlay-search {
  .keywords {
    padding-top: 0;
    padding-bottom: 30px;
  }
}

.overlay-onboarding {
  .flex-center-v-content {
    @include media-breakpoint-down(sm) {
      height: 100%; // fix mobile url bar covering top and bottom parts
    }
  }

  .overlay-wrap {
    $curve-top: 150px;

    @include media-breakpoint-down(sm) {
      max-width: none;
      height: 100%;

      &::before,
      &::after {
        content: '';
        position: absolute;
      }

      &::before {
        top: $curve-top;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to bottom, $color-blue-light, $color-blue);
      }

      &::after {
        top: $curve-top / 2;
        left: 50%;
        transform: translateX(-50%);
        background-color: $color-white;
        border-radius: 50%;
        width: 150vw;
        height: $curve-top;
      }
    }
  }

  .cancel {
    @include media-breakpoint-down(sm) {
      top: 10px;
      right: 10px;
    }
  }

  .onboarding-link {
    transition: color 0.4s $easeOutCubic;

    @include font(14, $letterSpacing: $font-ls-small);

    text-decoration: none;
    color: $color-grey-lighter;
    margin-top: 20px;
    margin-bottom: 20px;
    display: none;

    &:hover {
      color: $color-brand;
    }

    @include media-breakpoint-up(md) {
      display: inline-block;
    }
  }

  .overlay-content-onboarding {
    position: relative;
    z-index: 1;
    background-position: 100% center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: none;
    padding: ($grid-gutter-width / 2) 0;

    @include media-breakpoint-up(md) {
      background-image: url('./../images/onboarding/onboarding-desktop-01.png');
      padding: 30px 10px;
    }
  }

  .svg-logotype {
    max-width: 136px;
  }

  .onboarding-wrap {
    position: relative;
  }

  .onboarding-text {
    transition: opacity 0.5s $easeOutCubic, visibility 0.5s $easeOutCubic;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    visibility: hidden;
    opacity: 0;
    text-align: center;
    padding: 20px;

    @include media-breakpoint-up(md) {
      text-align: left;
      padding: 120px 20px;

      &:first-of-type {
        position: relative;
        visibility: visible;
        opacity: 1;
      }
    }

    &.is-active {
      @include media-breakpoint-down(sm) {
        position: relative;
        visibility: visible;
        opacity: 1;
      }
    }
  }

  .onboarding-header {
    margin-bottom: 10px;

    @include media-breakpoint-up(md) {
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 0;
      text-align: center;
    }
  }

  .onboarding-body {

  }

  p {
    color: $color-grey-light;

    @include media-breakpoint-down(sm) {
      @include font(15);
    }
  }

  // h2 {
  //   text-align: center;
  //   @include font(18);
  //   margin-bottom: 0;
  // }

  h1 {
    @include font(38, $letterSpacing: $font-ls-medium);
    @include font-notosanssc-medium;

    color: $color-brand;
  }

  .onboarding-gallery {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    margin-bottom: 0;
    display: block;

    @include media-breakpoint-up(md) {
      display: none;
    }

    .slick-dots {
      bottom: 10px;

      li {
        margin: 0;

        button {
          &::before {
            opacity: 1;

            @include font(10);

            color: $color-white;
          }
        }

        &.slick-active {
          button {
            &::before {
              color: $color-brand;
            }
          }
        }
      }
    }
  }

  .onboarding-gallery-slide {
    padding-top: 30px;
    padding-bottom: 30px;

    img {
      margin-left: auto;
      margin-right: auto;
      max-width: 240px;

      @include media-breakpoint-up(sm) {
        max-width: 400px;
      }
    }
  }
}

