            padding: 40px 15px 0;
          }

          h2 {
            @include font(24);
          }

          p {
            @include font (18);
          }
        }

        .overlay-body-container {
          // margin: 16px 0 0;
          width: 100%;
          overflow: scroll hidden;
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: flex-start;
          padding: 0 40px 30px;
          // padding-bottom: 30px;
          border-bottom: 1px solid $color-whitepale;

          @include media-breakpoint-up (md) {
            // padding: 0 2% 30px;
          }

          @include media-breakpoint-up (xl) {
            // margin: 24px 24px 0;
            // padding: 0 2% 30px;
            overflow: auto hidden;
          }
          // scroll-snap-align: center;
          // &::-webkit-scrollbar {
          //   visibility: visible;
          //   @include media-breakpoint-up (lg) {
          //     visibility: hidden;
          //   }
          // }

          .single-global-container {
            scroll-snap-align: center;
            -webkit-overflow-scrolling: touch;
            // display: inline-block;
            width: 280px;
            margin: 0 10px;

            @include media-breakpoint-up (md) {
              width: 342px;
              margin: 0 25px;
            }

            .pointer-container {
              padding-bottom: 5px;
              text-align: center;
              visibility: hidden;

              .caption {
                color: $color-brand;
                margin-bottom: 0;
                @include font(14);
              }

              img {
                width: 11px;
              }

              &.active {
                visibility: visible;
              }
            }

            .single-container {
              position: relative;
              // min-height: unset;
              height: 544px;
              box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
              background-color: $color-white;

              @include media-breakpoint-up (md) {
                height: 460px;
              }

              &.highlight {
