                  font-weight: 500;
                }
              }

              .cta-container {
                width: 100%;
                text-align: center;
                position: absolute;
                bottom: 28px;

                @include media-breakpoint-up (md) {
                  bottom: 30px;
                }
              }
            }
          }
        }

        .desktop-footer-container {
          margin: 0 auto;
          padding: 30px 0;
          text-align: center;
          display: none;

          @include media-breakpoint-up (md) {
            display: block;
          }

          img {
            width: 648px;
          }
        }

        .mobile-footer-container {
          display: block;
          margin: 0 30px;
          padding-top: 20px;
          padding-bottom: 20px;
          text-align: center;

          @include media-breakpoint-up (md) {
            display: none;
          }

          img {
            width: 100%;
          }
        }
      }
    }
  }
}

.overlay-fb-gallery {
  visibility: hidden;
  z-index: 4;
  opacity: 1;

  .overlay-wrap {
    transform: scale(1);
    max-width: 100%;
    width: 100%;

    @include media-breakpoint-up (md) {
      max-width: 720px;
      width: 720px;
    }

    .cancel {
      display: block;
      border: none;
      background: rgba(43, 43, 43, 0.8);
      top: 16px;
      right: 16px;

      @include media-breakpoint-up (sm) {
        top: -33px;
        right: -33px;
        background: none;
      }
    }

    .overlay-content {
      // width: 100%;
      overflow: unset;
      height: 100vh;
      max-height: 100vh;

      .slick-prev,
      .slick-next {
        &::before {
          content: '';
        }

        max-height: 100vh;

        img {
          height: 40px;
          width: 40px;
        }

        height: 40px;
        width: 40px;
      }

      .slick-prev {
        left: -56px;
      }

      .slick-next {
        right: -56px;
      }

      .slick-disabled {
        display: none !important;
      }

      @include media-breakpoint-up (sm) {
        max-height: unset;
        height: auto;
      }
    }

    .overlay-content-container {
      height: 100vh;
      width: 100%;
      display: block;
      position: relative;

      @include media-breakpoint-up (sm) {
        height: 360px;
        display: flex;
      }

      .overlay-image-container {
        width: 100%;
        overflow: hidden;
        height: 50vh;
        position: relative;

        @include media-breakpoint-up (sm) {
          width: 50%;
          height: auto;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }

        .overlay-thumbnails-container {
          width: 100%;
          height: 64px;
          position: absolute;
          bottom: 0;
          background-image: linear-gradient(rgba(43, 43, 43, 0) 30%, rgba(43, 43, 43, 0.9));

          .overlay-thumbnails {
            height: 100%;
            margin: 0 44px;
            display: flex;
            justify-content: center;

            a {
              border-radius: 4px;
              display: inline-block;
              width: 32px;
              height: 32px;
              background-position: center;
              background-size: cover;
              margin: auto 4px;

              &.is-active {
                border: 1px solid #ffffff;
              }
            }
          }
        }
      }

      .overlay-text-container {
        width: 100%;
        padding: 24px;
        height: 50vh;
        // position: relative;

        @include media-breakpoint-up (sm) {
          width: 50%;
          height: auto;
        }

        p {
          @include font(16);
          color: $color-black;
        }

        .overlay-text-header {
          padding-bottom: 16px;

          .logo {
            img {
              width: 32px;
              height: 32px;
              margin-right: 8px;
            }

            p {
              margin-bottom: 0;
              color: $color-grey-light;
            }
          }
        }

        .overlay-text-description {
          overflow-y: auto;
          height: 32vh;

          @include media-breakpoint-up (sm) {
            height: 244px;
          }

          // &::-webkit-scrollbar {
          //   display: none;
          // }
        }
      }

      .overlay-footer {
        position: absolute;
        bottom: 20px;
        width: 100%;
        left: 0;
        padding: 16px 24px 0;
        background-color: $color-white;

        @include media-breakpoint-up (sm) {
          width: 50%;
          left: 50%;
        }

        p {
          @include font (12);
          color: $color-grey-light;
        }
      }

      .logo-holder {
        width: 20px;
        height: 20px;
      }
    }
  }
}

