// new card style for new design moving forward
.card-list-col-2 {
  display: grid;
  grid-template-columns: repeat(1, 100%);

  @include media-breakpoint-up (lg) {
    display: flex;
    flex-direction: row;
  }

  li {
    .standard-card-container {
      // border-bottom: 1px solid $color-whitepale-light;

      @include media-breakpoint-up (lg) {
        &::before {
          display: none;
        }
      }
    }

    &:last-child {
      .standard-card-container {
        padding-bottom: 0;
        @include media-breakpoint-up (lg) {
          padding-bottom: 16px;
        }
      }
    }

    &:nth-child(1),
    &:nth-child(2) {
      .standard-card-container {
        @include media-breakpoint-up (lg) {
          padding-top: 0;
          border-top: none;
        }
      }
    }

    &:nth-child(3),
    &:nth-child(4) {
      .standard-vertical-card-container {
        &.quote-card {
          &.sm-quote-card {
            @include media-breakpoint-up (lg) {
              border-bottom: none;
            }
          }
        }
      }
    }

    &:nth-child(4) {
      .standard-vertical-card-container {
        &.quote-card {
          &.sm-quote-card {
            border-bottom: none;
            @include media-breakpoint-up (lg) {
              border-bottom: none;
            }
          }
        }
      }
    }

    &:nth-child(5),
    &:nth-child(6) {
      .standard-card-container {
        @include media-breakpoint-up (lg) {
          // padding-bottom: 0;
        }
      }
    }

    @include media-breakpoint-up (lg) {
      &:nth-child(5) {
        .standard-card-container {
          border-bottom: none;
        }
      }

      &:nth-child(6) {
        .standard-card-container {
          border-bottom: none;
        }
      }
    }
  }
}

.card-list-col-3 {
  display: flex;
  margin: 0;
  position: relative;

  @include media-breakpoint-up (lg) {
    margin: 0 -16px;
    // padding-top: 8px;
    // padding-bottom: 40px;
    display: flex;
    position: relative;

    &::before {
      position: absolute;
      left: 0;
      top: 0;
      content: ' ';
      height: 100%;
      z-index: 1;
      width: 16px;
      background: $white;
    }

    &::after {
      position: absolute;
      right: 0;
      top: 0;
      content: ' ';
      height: 100%;
      z-index: 1;
      width: 16px;
      background: $white;
    }
  }

  li {
    .standard-card-container {
      &::before {
        content: ' ';
        width: 16px;
        height: auto;
        border-left: none;
        display: none;

        @include media-breakpoint-up (lg) {
          display: block;
          border-left: 1px solid $color-whitepale;
          width: 16px;
          height: auto;
          padding-left: 16px;
        }
      }
    }

    // &:last-child {
    //   .standard-card-container {
    //     border-bottom: none;
    //   }
    // }

    @include media-breakpoint-up (lg) {
      &:nth-child(-n + 3) {
        .standard-card-container {
          border-top: none;
        }
      }

      &:nth-child(n + 7) {
        .standard-card-container {
          border-bottom: none;
        }
      }

      &:nth-child(3n + 1) {
        .standard-card-container {
          &::before {
            content: ' ';
            width: 16px;
            height: auto;
            border-left: none;
          }
        }
      }
    }
  }
}

.breaking-news-container {
  // to make sure last child no line
  // padding-bottom: 16px;
  // border-bottom: 1px solid $color-whitepale;

  ul {
    padding-left: 0;

    li {
      list-style-type: none;

      &:last-child {
        .ticker-card {
          .ticker {
            &::before {
              content: none;
            }
          }
        }
      }
    }
  }
}

.card {
  border: none;
  background: none;

  & > .card-content {
    a {
      &:hover {
        text-decoration: none;
        color: inherit;
      }
    }
  }

  &.ticker-card {
    height: 52px;
    margin-bottom: 1.6rem;
    // display: grid;
    // grid-template-columns: 30% 70%;

    .ticker {
      display: flex;
      justify-content: flex-start;
      align-items: start;
      position: relative;

      p {
        @include font(14);
        color: #e90044;
        margin-bottom: 0;
      }

      .ticker-dot {
        margin-right: 8px;
        height: 7px;
        width: 7px;
        border-radius: 50%;
        background-color: #e90044;
        z-index: 1;
        margin-top: 6px;
      }

      &::before {
        content: ' ';
        height: 62px;
        width: 1px;
        position: absolute;
        top: 12px;
        left: 3px;
        background-color: $color-whitepale;
      }
    }

    .card-content {
      h3 {
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        @include font(18);
        font-weight: 400;
      }

      a {
        &:hover {
          text-decoration: none;
          color: inherit;
        }
      }
    }
  }

  &.standard-card-container {
    position: relative;
    display: flex;
    flex-direction: row;
    // max-height: 120px;
    // height: unset;
    padding: 16px 0;
    border-radius: 0;
    position: relative;
    border-top: 1px solid $color-whitepale;
    justify-content: space-between;

    @include media-breakpoint-up (lg) {
      // min-height: 90px;
    }

    .card-content {
      width: auto;
      padding-right: 8px;
      height: 74px;

      @include media-breakpoint-up (lg) {
        padding-right: 8px;
      }

      h3 {
        @include font(18);
        letter-spacing: 0.5px;
        font-family: 'Noto Sans SC', Arial, sans-serif;
        line-height: 26px;
        font-weight: 400;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        margin-bottom: 0;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }

      // &.no-image {
      //   width: 100%;
      //   padding-right: 0;

      //   h3 {
      //     @include font(18);
      //     letter-spacing: 0.5px;
      //     font-family: 'Noto Sans SC', Arial, sans-serif;
      //     line-height: 1.45;
      //     font-weight: 400;
      //     display: -webkit-box;
      //     -webkit-line-clamp: 2;
      //     -webkit-box-orient: vertical;
      //     overflow: hidden;
      //   }
      // }

      .time-card {
        position: absolute;
        bottom: 1.6rem;
        p {
          @include font(12);
          color: $color-grey-light;
          margin-bottom: 0;
        }
      }
    }

    .card-image-container {
      max-width: 44%;
      overflow: hidden;
      border-radius: 4px;
      aspect-ratio: 3 / 2;
      display: block;

      @include media-breakpoint-up (lg) {
        margin-right: 16px;
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    &.no-image {
      .card-content {
        width: auto;
        @include media-breakpoint-up (lg) {
          width: 100%;
        }
        padding-right: 16px;

        h3 {
          @include font(18);
          letter-spacing: 0.5px;
          font-family: 'Noto Sans SC', Arial, sans-serif;
          line-height: 1.45;
          font-weight: 400;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }

      .card-image-container {
        max-width: 44%;
        @include media-breakpoint-up (lg) {
          display: none;
        }
      }
    }
  }

  &.standard-vertical-card-container {
    // padding-top: 1.6rem;
    display: block;
    margin: 16px 0;

    @include media-breakpoint-up(lg) {
      margin: 0;
    }

    .card-content {
      h3 {
        @include font(20);
        letter-spacing: 0.5px;
        font-family: 'Noto Sans SC', Arial, sans-serif;
        line-height: 1.45;
        font-weight: 500;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }

      .time-card {
        p {
          @include font (12);
          font-weight: 400;
          color: $color-grey-light;
        }
      }

      @include media-breakpoint-up (lg) {
        border-bottom: none;
      }
    }

    .card-image-container {
      padding-bottom: 1.6rem;
      display: block;
      position: relative;

      img {
        width: 100%;
        aspect-ratio: 3 / 2;
        height: 100%;
        object-fit: cover;
        overflow: hidden;
        object-position: center;
        border-radius: 4px;
      }

      .duration-card {
        position: absolute;
        bottom: 24px;
        right: 8px;
        padding: 0 8px;
        background-color: rgba(0, 0, 0, .4);
        border-radius: 16px;

        p {
          color: $white;
          margin-bottom: 0;
          @include font (14);
        }
      }
    }

    &.quote-card {
      padding-bottom: 16px;
      border-bottom: 1px solid $color-whitepale-light;

      @include media-breakpoint-up (lg) {
        border-bottom: none;
      }

      .card-content {
        display: block;
        position: relative;
        height: 75px;
        // justify-content: flex-start;

        &::before {
          content: ' ';
          position: absolute;
          display: inline-block;
          background-image: url('./../images/icon/quote-icon.png');
          background-repeat: no-repeat;
          background-position: center;
          background-size: 18px 15px;
          width: 26px;
          height: 26px;
          margin-right: 4px;
        }

        .card-content-container {
          display: inline-block;
          padding-left: 30px;

          h3 {
            font-weight: 500;
            @include font(20);
            color: $color-grey-900;
            line-height: 144.444%;
          }
        }
      }

      .editor-profile {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: absolute;
        bottom: 0;
        right: 0;

        h4 {
          margin-bottom: 0;
          color: $color-grey-light;
        }

        img {
          border-radius: 50%;
          width: 45px;
          height: 45px;
          margin-left: 8px;
        }

        a {
          &:hover {
            color: inherit;
            text-decoration: none;
          }
        }
      }

      &.sm-quote-card {
        height: unset;
        padding: 1.6rem 0;
        border-bottom: 1px solid $color-whitepale;

        @include media-breakpoint-up (lg) {

        }

        .card-content {
          height: 100px;

          .card-content-container {
            display: inline-block;
            padding-left: 30px;

            h3 {
              font-weight: 400;
              @include font(18);
              color: $color-grey-900;
              line-height: 144.444%;
            }
          }
        }
      }
    }

    &.inverse {
      .card-content {
        h3 {
          color: $content-primary-inverse;
        }
      }
    }
  }

  &.news-feature-card-container {
    display: flex;
    flex-direction: column;
    position: relative;
    // flex-wrap: wrap;
    border-bottom: none;
    margin-right: -15px;
    margin-left: -15px;

    @include media-breakpoint-up (lg) {
      margin-right: 0;
      margin-left: 0;
      display: flex;
      flex-direction: row;
      // flex-wrap: wrap;
      padding-bottom: 24px;
      border-bottom: 1px solid $color-whitepale;
    }

    .card-header {
      display: flex;
      align-items: center;
      padding: 0;
      background: none;
      border-bottom: none;
      padding-left: 16px;
      padding-bottom: 16px;
      position: relative;
      order: 1;
      color: $color-brand;

      @include media-breakpoint-up(lg) {
        padding-bottom: 24px;
        padding-left: 0;
        order: unset;
        position: absolute;
        top: 0;
      }

      img {
        width: 20px;
        height: 20px;
        margin-right: 8px;
      }

      h1 {
        margin-bottom: 0;
      }
    }

    .card-content {
      position: relative;
      width: 100%;
      padding: 15px;
      //margin: 0 15px;
      // border-bottom: 1px solid $color-whitepale-light;
      order: 3;

      @include media-breakpoint-up (lg) {
        flex: 0 0 41.66667%;
        max-width: 41.66667%;
        order: 0;
        padding: 0 15px 0 0;
        margin: 56px 0 0 0;
        border-bottom: none;
      }

      h2 {
        margin-bottom: 1.6rem;
        line-height: 145.455%;
      }

      p {
        color: $color-grey-light;
        @include font(16);
        display: none;

        @include media-breakpoint-up (lg) {
          display: -webkit-box;
          -webkit-line-clamp: 6;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
    }

    .card-image-container {
      position: relative;
      width: 100%;
      padding: 0;
      order: 2;
      // margin: 0 -15px;

      @include media-breakpoint-up (lg) {
        flex: 0 0 58.33333%;
        max-width: 58.33333%;
      }

      img {
        width: 100%;
        aspect-ratio: 3 / 2;
        height: 100%;
        object-fit: cover;
        overflow: hidden;
        object-position: center;
        border-radius: 0;

        @include media-breakpoint-up (lg) {
          border-radius: 4px;
        }
      }
    }

    .divider {
      &.divider-x {
        display: block;
        @include media-breakpoint-up (lg) {
          display: none;
        }
      }
    }
  }

  &.section-single-card {
    display: flex;

    .content-container {
      width: 100%;
      padding-right: 16px;

      @include media-breakpoint-up(lg) {
        width: 65%;
      }

      &:hover {
        text-decoration: none;
        // cursor: pointer;
        h3 {
          color: $color-brand;
        }
      }

      h3 {
        margin-bottom: 0;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: $color-grey;

        @include media-breakpoint-up(lg) {
          margin-bottom: 10px;
        }
      }

      p {
        display: none;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        margin-bottom: 0;
        color: $color-grey-light;

        @include media-breakpoint-up (lg) {
          display: -webkit-box;
        }
      }
    }

    .img-container {
      width: 35%;

      img {
        border-radius: 4px;
        width: 100%;
        object-fit: cover;
        object-position: center;
        height: auto;
        @include media-breakpoint-up(lg) {
          height: 105px;
        }
        // max-width: 210px;
      }
    }

    .card-body {
      padding: 20px 0;
      margin: 0;
      border-bottom: 1px solid $color-whitepale;

      @include media-breakpoint-up(lg) {
        margin: 0 0 0 20px;
      }
    }
  }

  &:last-child {
    .card-body {
      border-bottom: none;
      // padding-bottom: 0;
    }
  }

  &.section-main-card {
    .card-body {
      padding: 0;
      border-right: none;
      margin-top: 20px;

      @include media-breakpoint-up (lg) {
        border-right: 1px solid $color-whitepale;
        padding: 0 20px 0 0;
      }

      .content-container {
        display: none;
        height: unset;
        max-height: unset;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        overflow: hidden;

        @include media-breakpoint-up (lg) {
          height: 396px;
          max-height: 396px;
          display: block;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
          border-radius: 4px;
        }
      }

      .content-container-mobile {
        display: block;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        overflow: hidden;

        @include media-breakpoint-up (sm) {
          background-size: 100%;
        }

        @include media-breakpoint-up (lg) {
          display: none;
        }

        img {
          width: 100%;
          height: auto;
        }
      }
    }
  }
}

.item-type-container {
  aspect-ratio: 3 / 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .item-type {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.4);

    img {
      margin: 10px;
      width: 20px !important;
      height: 20px !important;
      border-radius: 0 !important;
    }
  }
}

.section-carousel {
  // margin-top: 40px;
  width: 100%;
  display: flex;

  .section-banner {
    display: block;
    // min-width: 360px;
    // margin-right: 0;

    @include media-breakpoint-up (lg) {
      // margin-right:20px;
    }
    // &:last-child {
    //   padding-right: 0;
    // }

    img {
      padding: 0;
      @include media-breakpoint-up (lg) {
        padding: 0 10px;
      }
      width: 100%;
    }
  }

  .slick-dots {
    bottom: -16px;

    li {
      margin: 0 4px;
      width: unset;
      height: auto;
      padding: 0;

      button {
        width: 32px;
        height: 4px;
        padding: 0;
        border-radius: 100px;
        background-color: $content-primary-inverse;

        &::before {
          content: '';
          font-size: 8px;
          color: $white;
          opacity: 1;
        }
      }

      &.slick-active {
        button {
          position: relative;
          // animation-name: loader;
          // animation-duration: 5s;
          width: 32px;

          &::before {
            position: absolute;
            left: 0;
            content: '';
            height: 4px;
            width: 0;
            color: none;
            border-radius: 100px;
            background-color: $color-brand;
            animation-name: loader;
            animation-duration: 5s;
          }
        }
      }
    }
  }

  .slick-list {
    margin: 0;
    @include media-breakpoint-up (lg) {
      margin: 0 -10px;
    }
  }

  .slick-next {
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    width: 36px;
    height: 36px;
    border: 1px solid #9e9e9e;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 50%;

    @include media-breakpoint-up (lg) {
      // display: flex;
      width: 36px;
      // top: unset;
      // bottom: -2px;
      right: 16px;
    }

    img {
      height: 20px;
    }

    &::before {
      content: '';
    }
  }

  .slick-prev {
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    width: 36px;
    height: 36px;
    border: 1px solid #9e9e9e;
    border-radius: 50%;
    z-index: 1;
    background: rgba(255, 255, 255, 0.6);

    @include media-breakpoint-up (lg) {
      width: 36px;
      left: 16px;
      // top: unset;
      // bottom: -2px;
    }

    img {
      height: 20px;
    }

    &::before {
      content: '';
    }
  }
}

.padding-divider-card {
  display: block;
  padding-top: 5px;

  &.sm {
    padding-top: 10px;
  }

  &.md {
    padding-top: 20px;
  }

  &.lg {
    padding-top: 40px;
    @include media-breakpoint-up (lg) {
      padding-top: 40px;
    }
  }
}
// animation
@keyframes loader {
  from {
    width: 0;
  }

  to {
    width: 32px;
  }
}
