// =====================================================================
// Pagination
// =====================================================================

.pagination {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;

  @include media-breakpoint-up(md) {
    text-align: left;
  }

  @include media-breakpoint-up(lg) {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .row-pagination {
    width: 100%;

    @include media-breakpoint-up(lg) {
      flex: nowrap;
    }
  }
}

.pagination-list {
  $row-tighten: 8px;

  @include reset-list;

  width: 100%;
  margin: 20px 0;

  > [class*='col-'] {
    padding-left: $row-tighten;
    padding-right: $row-tighten;
  }

  @include media-breakpoint-up(md) {
    margin: 0;
  }

  @include media-breakpoint-up(lg) {
    flex-wrap: nowrap;
  }

  > li {
  }
}

.pagination-link {
  transition: background-color 0.4s $easeOutCubic, color 0.4s $easeOutCubic;
  color: $color-grey-light;

  @include font(15, $letterSpacing: $font-ls-small);

  padding: 5px;

  @include media-breakpoint-up(md) {
    @include font(14, $letterSpacing: $font-ls-small);

    padding: 0;
  }

  &.is-active {
    color: $color-brand;

    @include underline(0.5, -2px);
  }

  &:hover {
    text-decoration: none;
    color: $color-brand;
  }

  &[href='javascript:void(0)'] {
    cursor: auto;

    &:hover {
      color: $color-grey-light;
    }
  }

  &[disabled] {
    cursor: auto;
    color: $color-whitepale;
    border-color: $color-whitepale;

    &:hover {
      background-color: transparent;
      color: $color-whitepale;
    }
  }
}

.pagination-link-prev,
.pagination-link-next {
  display: inline-block;
  height: 30px;
  line-height: 28px; // align visually
  border-radius: 15px;
  border: 1px solid $color-grey-light;
  padding: 0 12px;

  @include media-breakpoint-up(md) {
    padding: 0 15px;
  }

  span {
    display: none;
  }

  &:hover {
    background-color: $color-grey-light;
    color: $color-white;
  }

  @include media-breakpoint-up(md) {
    span {
      display: inline-block;
    }
  }
}

.pagination-link-prev {
  .fa-caret-left {
    @include media-breakpoint-up(md) {
      margin-right: 8px;
    }
  }
}

.pagination-link-next {
  .fa-caret-right {
    @include media-breakpoint-up(md) {
      margin-left: 8px;
    }
  }
}
