
.iframe-9-16 {
  position: relative;
  display: block;
  max-width: 48.5%;
  padding-top: 100%;
  margin: 0 auto 1rem;

  @include media-breakpoint-down(xs) {
    padding-top: 100%;
    max-width: 74%;
    height: 70vh;
    margin: 0 auto 1rem;
  }

  @media (max-width: 414.98px) {
    padding-top: 100%;
    max-width: 80%;
    height: 70vh;
    margin: 0 auto 1rem;
  }

  &.iframe-tiktok {

  }

  iframe {
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border: 0;
    border-radius: 0.2778rem;
    margin: 0 auto;
  }
}
