// =====================================================================
// Article-type (listing)
// =====================================================================

$meta-margin-y: 6px;

.article-type {
  padding: 10px 0;

  @include media-breakpoint-up(lg) {
    padding: 20px 0;
  }

  h2 {
    line-height: 1.33;

    @include lineclamp(2);

    @include media-breakpoint-up(lg) {
      @include lineclamp(3);
    }
  }

  .cta-play {
    position: absolute;
    left: 10px;
    bottom: 10px;

    @include media-breakpoint-down(md) {
      padding-left: 10px;
      padding-right: 10px;
    }

    .cta-play-meta {
      @include media-breakpoint-down(md) {
        @include font(12, $letterSpacing: $font-ls-small);
      }
    }
  }

  .article-type-img {
    position: relative;
  }

  .article-type-img-link {
    display: block;
    position: relative;

    @include aspect-ratio(8, 5);

    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 0;

    @include media-breakpoint-up(lg) {
      @include aspect-ratio(3, 2);
    }

    img {
      width: 100%;
    }
  }

  .article-type-author {
    transition: box-shadow 0.4s $easeOutCubic;
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    border-radius: 50%;
    background-color: $color-white;
    color: $color-brand;
    right: 5px;
    bottom: 5px;
    margin-bottom: 0;

    @include font(12, $letterSpacing: $font-ls-small);

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    &:hover {
      text-decoration: none;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    }
  }

  &.article-type-a {
    .article-type-img {
      width: 100px;

      @include media-breakpoint-up(lg) {
        width: auto;
      }
    }

    h2 {
      margin-bottom: 10px;
    }

    .blurb {
      @include font(16, $letterSpacing: $font-ls-small);
    }
  }

  &.article-type-b {
    .article-type-img {
      width: 177px;
      height: 118px;
      border-radius: 5px;
      overflow: hidden;

      @include media-breakpoint-up(lg) {
        width: auto;
        border-radius: 0;
      }
    }
  }

  &.article-type-c {
    .article-type-img {
      width: 177px;
      height: 118px;
      border-radius: 5px;
      overflow: hidden;

      @include media-breakpoint-up(lg) {
        width: auto;
        height: 100%;
        border-radius: 0;
      }
    }

    .article-type-meta {
      @include media-breakpoint-up(lg) {
        margin-top: $meta-margin-y;
      }
    }

    .article-type-content {
      @include media-breakpoint-up(lg) {
        padding-left: 0;
      }
    }
  }

  &.article-type-d {
    .article-type-meta {
      margin-top: $meta-margin-y;
    }

    .article-type-content {
      padding-left: 0;
    }

    h2 {
      @include font(24, $letterSpacing: $font-ls-small);

      margin-bottom: 10px;
    }
  }

  &.article-type-e {
    .article-type-content {
      padding-left: 0;
    }

    .article-type-meta {

    }

    .article-type-link {

    }

    h2 {
      @include font(16, $letterSpacing: $font-ls-small);

      margin-bottom: 0;

      @include truncate;
    }
  }

  &.article-type-gallery {
    h2 {
      @include media-breakpoint-up(xl) {
        @include truncate;
      }
    }

    .article-type-meta {
      margin-top: 0;
    }

    .article-type-content {
      padding-left: 0;
    }

    figure {
      margin-bottom: 0;
    }

    figcaption {
      text-align: left;
      padding-bottom: 0;
    }

    &.article-type-fb {
      padding: 0;
    }
  }

  &.article-type-video {
    .article-type-content {
      background-color: $color-whitepale-light;
      padding: 8px $grid-gutter-width / 2;

      @include media-breakpoint-up(lg) {
        min-height: 140px;
      }
    }

    .article-type-bgtint {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: transparentize($color-black, 0.8);
    }

    .article-type-link {
      h2 {
        @include media-breakpoint-up(lg) {
          @include truncate;
        }
      }

      .description {
        @include media-breakpoint-up(lg) {
          @include lineclamp(3);
        }
      }
    }

    .svg-icon-play {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
}

// used in figure-media-gallery caption too
.article-type-content {
  padding-left: 10px;

  h2 {
    transition: color 0.3s $easeOutCubic;

    @include font(18);
    @include font-notosanssc-regular;

    color: $color-grey;
  }

  p {
    transition: color 0.3s $easeOutCubic;
  }

  .article-type-meta {
    margin-bottom: 2px; // agaration
  }

  .article-type-link {

  }

  .meta {
    border-right: 1px solid $color-whitepale;
    padding: 0 10px;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
      border-right: 0;
    }

    &.meta-category {
      a {
        transition: color 0.3s $easeOutCubic;

        @include font(14);

        color: $color-grey-light;

        &:hover {
          color: $color-grey;
        }
      }
    }

    &.meta-publication {
      &.meta-publication-wanbao {
        a {
          color: $color-blue-wanbao;
        }
      }
    }

    &.meta-published-date {
      @include font(12);

      color: $color-grey-light;
      padding-bottom: 10px;
    }
  }

  .blurb {
    @include font(18, $letterSpacing: $font-ls-small);

    color: $color-grey-light;
  }

  .description {
    @include font(16, $letterSpacing: $font-ls-small);

    color: $color-grey;
  }

  a {
    &:hover {
      text-decoration: none;

      h2 {
        color: $color-brand;
      }

      p {
        color: $color-grey;
      }
    }
  }
}
