// =====================================================================
// Topnavbar (Tertiary)
// =====================================================================
$miscellaneous-item-w: 128px;

.topnavbar-tertiary {
  z-index: 1;
  background-color: $color-whitepale-light;
  overflow: hidden;

  @include media-breakpoint-up(lg) {
    box-shadow: 0 0 16px transparentize($color-black, 0.75);
  }

  .topnav-link {
    display: block;
  }

  h2 {
    @include font(16);

    color: $color-brand;
  }

  p {
    transition: color 0.4s $easeOutCubic;

    @include font(14);

    color: $color-white;

    @include media-breakpoint-up(sm) {
      color: $color-grey;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.topnavbar-miscellaneous {
  max-width: $maxw-xl;
  margin-left: auto;
  margin-right: auto;
  padding: 6px;

  @include media-breakpoint-up(sm) {
    padding: 0;
  }

  .topnavbar-miscellaneous-list {
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    white-space: nowrap;
    font-size: 0;

    li {
      display: inline-block;
      vertical-align: top;
      width: $miscellaneous-item-w;
      margin-right: 6px;

      &:last-of-type {
        margin-right: 0;
      }

      @include media-breakpoint-up(sm) {
        min-width: 320px;
        width: 25%;
        margin-right: 0;
      }

      @include media-breakpoint-up(lg) {
        min-width: auto;
      }
    }
  }

  .miscellaneous-cta {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }

  .miscellaneous-img {
    position: relative;
    flex: 0 0 auto;
    background-size: auto 1200%;
    // @include aspect-ratio(8, 5);

    animation: horoscope 24s steps(12) infinite;
    width: $miscellaneous-item-w;
    height: 80px;

    h2 {
      position: absolute;
      top: 8px;
      left: 8px;
      display: none;

      @include media-breakpoint-up(lg) {
        display: block;
      }

      @include media-breakpoint-up(xl) {
        display: none;
      }
    }

    a {
      position: absolute;
      z-index: 1;
      left: 0;
    }

    img {
      width: 100%;
    }

    .label-premium {
      position: absolute;
      z-index: 1;
      left: 8px;
      top: 8px;
      bottom: auto;

      @include media-breakpoint-up(sm) {
        top: auto;
        bottom: 8px;
      }
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;

      @include media-breakpoint-up(sm) {
        display: none;
      }
    }

    &::before {
      top: 0;
      background: linear-gradient(to bottom, transparentize($color-black, 0.3), transparentize($color-black, 1));
    }

    &::after {
      bottom: 0;
      background: linear-gradient(to bottom, transparentize($color-black, 1), transparentize($color-black, 0.3));
    }
  }

  .miscellaneous-text {
    white-space: normal;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 8px;

    @include media-breakpoint-up(sm) {
      position: relative;
    }

    @include media-breakpoint-up(lg) {
      padding: 7px 10px;
    }

    @include media-breakpoint-up(xl) {
      padding: 7px 15px;
    }

    h2 {
      display: none;

      @include media-breakpoint-up(sm) {
        display: block;
      }

      @include media-breakpoint-up(lg) {
        display: none;
      }

      @include media-breakpoint-up(xl) {
        display: block;
      }
    }

    p {
      @include lineclamp(2);
    }
  }
}
