// =====================================================================
// Typography ( t )
// =====================================================================

html {
  @include font-smoothing(on);

  font-size: $font-root-size;
}

body {
  @include font-notosanssc-regular;
}

h1,
h2,
h3,
h4,
h5,
h6 {

}

h1 {
  @include font(36, $letterSpacing: $font-ls-small);
  @include font-notosanssc-regular;

  .page-home & {
    @include font(24, $letterSpacing: $font-ls-small);
  }
}

h2 {
  @include font(22, $letterSpacing: $font-ls-small);
  @include font-notosanssc-medium;

  .page-home & {
    @include font(22, $letterSpacing: $font-ls-small);
  }
}

h3 {
  @include font(20, $letterSpacing: $font-ls-small);
  @include font-notosanssc-medium;

  .page-home & {
    @include font(18, $letterSpacing: $font-ls-small);
    @include font-notosanssc-regular;
  }
}

h4 {
  .page-home & {
    @include font(16, $letterSpacing: $font-ls-small);
  }
}

h5 {
  .page-home & {
    @include font(14, $letterSpacing: $font-ls-small);
  }
}

h6 {
  .page-home & {
    @include font(12, $letterSpacing: $font-ls-small);
  }
}

a,
p {
  @include font($font-base-size, $letterSpacing: $font-ls-small);

  color: $color-body;
}

p {
  @include font-notosanssc-regular;

  &.medium {
    @include font(16, $letterSpacing: $font-ls-small);
  }

  &.small {
    @include font(14, $letterSpacing: $font-ls-small);
  }

  &.white {
    color: $color-white;
  }

  &.light-grey {
    color: $color-grey-light;
  }
}

strong {
  @include font-notosanssc-bold;
}

.font-notosanssc-medium {
  @include font-notosanssc-medium;
}

.font-notosanssc-bold {
  @include font-notosanssc-bold;
}
