// =====================================================================
// Topnavbar (Primary)
// =====================================================================

.topnavbar-primary {
  background-color: $color-white;
  z-index: 3;

  .topnav-link {
    @include font(14, $cta-h);
    @include font-notosanssc-regular;

    color: $color-grey;
    padding: 0;

    &:hover {
      color: $color-brand;
    }
    padding: 0 15px;

    &.logotype {
      position: relative;
      .confetti {
        position: absolute;
        top: -16px;
        right: 4%;
        width: 315px;
        height: 78px;
        overflow: hidden;

        .animation {
          transform: translateY(-114px) !important;
          width: 315px !important;
          height: 315px !important;
          display: block;
        }
      }
    }
  }

  .topnavbar-featured {
    padding: 15px 0;
  }

  .divider-line {
    height: 30px;
    width: 1px;
    background-color: $color-whitepale;
  }

  .cta-holder-subscribe {
    display: flex;
    float: right;
    overflow: visible;
    margin-left: 15px;

    .cta-grey,
    .cta-solid-red {
      @include font (16);
      letter-spacing: 1px;
      padding: 0 24px;
    }

    .cta-gradient-red {
      @include font (16);
      letter-spacing: 1px;
      padding: 0 16px;
    }
  }

  .cta-holder-user {
    display: none;
    overflow: visible;

    .no-touchevents & {
      &:hover {
        background-color: transparent;
        box-shadow: none;
      }
    }
  }
}
