// =====================================================================
// Footer (footer)
// =====================================================================

.footer {
  padding: 50px 0;
  background-color: $color-grey;

  ul {
    @include reset-list;
  }

  p,
  a {
    @include font(14);

    color: $color-white;
  }

  .fa,
  .fab {
    @include font(18);

    // background: $color-grey-lighter;
  }

  .logotype {
    margin-bottom: 20px;
  }

  .footer-border {
    border-top: solid 1px rgba(0, 0, 0, 0.5);
    width: calc(100% - 30px);
    margin: 30px auto;
  }

  .footer-main {
    .footer-main-list {
      margin-right: 0;

      @include media-breakpoint-up(xl) {
        margin-right: 15px;
      }

      @include media-breakpoint-up(xxl) {
        margin-right: 45px;
      }

      > li {
        > a {
          @include font(16);
          @include font-notosanssc-medium;

          display: block;
          margin-bottom: 10px;
        }
      }
    }

    .footer-main-sublist {
      margin-bottom: 20px;

      > li {
        > a {
          color: $color-grey-lighter;
        }
      }
    }

    .footer-main-links {
      @extend %l-footer-main-mb;

      display: none;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }

    .footer-main-contact {
      @extend %l-footer-main-mb;

      a {
        @include font(16);
        @include font-notosanssc-medium;

        display: block;
        margin-bottom: 20px;

        @include media-breakpoint-up(md) {
          margin-bottom: 10px;
        }
      }
    }

    .footer-main-social {
      @extend %l-footer-main-mb;

      .fa,
      .fab {
        margin-top: 10px;
        margin-bottom: 10px;

        // &:hover {
        //   color: $color-grey;
        // }
      }
    }

    .footer-main-apps {
      margin-top: 0;
      margin-bottom: 0;

      @include media-breakpoint-up(sm) {
        margin-top: 10px;
        margin-bottom: 10px;
      }

      a {
        display: inline-block;
        margin-bottom: 10px;
        margin-left: 10px;
        margin-right: 10px;

        @include media-breakpoint-up(sm) {
          margin-left: 0;
          margin-right: 6px;
        }

        img {
          max-width: 100%;

          @include media-breakpoint-up(sm) {
            max-width: 90px;
          }
        }
      }
    }

    .footer-main-numbers {
      @extend %l-footer-main-mb;

      p {
        color: $color-grey-lighter;
      }
    }
  }

  .footer-sub {
    p {
      color: $color-grey-lighter;
    }

    .logo-footer {
      margin-bottom: 30px;

      @include media-breakpoint-up(md) {
        margin-bottom: 20px;
      }
    }

    .footer-sub-list {
      margin-bottom: 10px;

      @include media-breakpoint-up(md) {
        margin-bottom: 50px;
      }

      li {
        display: inline-block;
        margin-right: 20px;
        margin-bottom: 10px;

        @include media-breakpoint-up(md) {
          margin-bottom: 0;
        }
      }
    }
  }

  .order-swap {
    > [class*='col'] {
      &:first-of-type {
        order: 2;
      }

      &:last-of-type {
        order: 1;
      }
    }

    @include media-breakpoint-up(md) {
      > [class*='col'] {
        &:first-of-type {
          order: 1;
        }

        &:last-of-type {
          order: 2;
        }
      }
    }
  }
}
