// =====================================================================
// Mobile Social
// =====================================================================

.mobile-social {
  transition: transform 0.8s $easeOutCubic;
  position: fixed;
  z-index: 8;
  top: calc(65% - 30px);
  height: 60px;
  background-color: $color-brand;
  color: $color-white;
  transform: translate3d(-100%, 0, 0);
  display: block;

  @include media-breakpoint-up(lg) {
    display: none;
  }

  .aside-social {
    padding-left: 8px;
    padding-right: 8px;

    .a2a_kit {
      line-height: 60px !important; // stylelint-disable-line declaration-no-important

      .aside-social-icon {
        display: inline-block;
      }

      a {
        display: inline-block;
        vertical-align: middle;
        margin-left: 8px;
        margin-right: 8px;
      }
    }
  }

  &.is-active {
    transition: transform 0.7s $easeInOutCubic;
    transform: translate3d(0, 0, 0);

    .is-scrolled-up &,
    .is-scrolled-down & {
      transform: translate3d(0, 0, 0);
    }

    &.is-sidelined {
      transform: translate3d(-100%, 0, 0);
    }
  }
}

.mobile-social-handle {
  position: absolute;
  text-align: center;
  top: 0;
  right: 0;
  width: 70px;
  height: 100%;
  padding-right: 6px; // agaration
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  background-color: $color-brand;
  transform: translate3d(100%, 0, 0);

  @include font(18, 60px);
}
