// ================
// redirect
// ================
.redirect-banner-container {
  background: $color-whitepale-light;
  height: auto;

  @include media-breakpoint-up (sm) {
    height: 86px;
  }

  .redirect-header {
    float: right;
    display: block;

    .redirect-cancel {
      padding-top: 10px;
      padding-right: 27px;
      width: 30px;
      cursor: pointer;
    }
  }

  .redirect-content {
    display: flex;
    justify-content: center;

    .redirect-msg {
      margin: 15px auto;
      padding-left: 30px;

      @include media-breakpoint-up (lg) {
        padding-left: 0;
        margin: 30px auto;
      }
    }
  }

  a {
    color: $color-brand;
    // text-decoration: underline;
  }
}
