.article-content-rawhtml {
  color: $color-grey;

  p {
    a {
      @include underline-border(0.5, 0);

      color: $color-blue-900;

      &.link-microtransaction {
        color: $color-black;
      }

      &.link-article {
        color: $color-grey-lighter;
      }
    }

    img {
      max-width: 100%;
    }
  }
}

.article-related-podcast {
  @extend %l-widget-spacing;

  iframe {
    @extend %l-widget-spacing;
  }
}

.article-related-video {
  @extend %l-widget-spacing;

  background-color: $color-brand;
  border-radius: 8px;

  .description {
    padding: 20px ($grid-gutter-width / 2);
  }

  .description-content {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 5px;
  }

  h2 {
    color: $color-white;

    @include font-notosanssc-regular;
    @include font(24, $letterSpacing: $font-ls-regular);
  }

  .category {
    p {
      color: $color-white;

      @include font(16);
      @include font-notosanssc-medium;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  .blurb-holder {
    .category {
      margin-bottom: 0;

      @include media-breakpoint-up(xl) {
        margin-bottom: 40px;
      }
    }

    .cta-play {
      margin: 20px 0;
      display: none;

      @include media-breakpoint-up(xl) {
        display: inline-block;
      }
    }

    h2 {
      display: none;

      @include media-breakpoint-up(xl) {
        display: block;
      }
    }
  }

  .tn-holder {
    @include aspect-ratio(3, 2);

    .svg-icon-play-bordered {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }

    .description {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;

      @include media-breakpoint-up(xl) {
        display: none;
      }

      h2 {
        position: absolute;
        left: 0;
        bottom: 0;
      }
    }

    img,
    .video {
      overflow: hidden;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      display: block;

      @include media-breakpoint-up(xl) {
        max-width: 380px;
        border-bottom-left-radius: 0;
        border-top-right-radius: 8px;
      }

      @include media-breakpoint-up(xxl) {
        max-width: 440px;
      }
    }
  }
}

.article-video-series {
  @extend %l-widget-spacing;

  .banner {
    width: 100%;
  }

  .description {
    margin: 20px 0;

    a {
      color: $color-brand;

      @include underline-border(0.5, 0);
    }
  }

  h2 {
    @include font(16);

    color: $color-grey-light;
    margin-bottom: 10px;
    padding: 0 10px;
  }

  .episodes {
    display: flex;
    flex-direction: column;
  }

  .episode-link {
    transition: background-color 0.4s $easeInOutCubic;
    padding: 10px;
    order: 2;

    &:hover {
      text-decoration: none;
      background-color: $color-whitepale;
    }

    p {
      margin-bottom: 4px;

      &.small {
        color: $color-grey-light;
      }
    }

    &.is-active {
      background-color: $color-whitepale;
      order: 1;

      p {
        color: $color-brand;
      }
    }
  }

  .tn {
    max-width: 90px;
  }
}

.article-comments-fb {
  @extend %l-widget-spacing;
}

.article-microtransaction {
  margin: 20px 0;
}

.article-share-fb {
  @extend %l-widget-spacing;
  @extend %l-aside-item-padding-y;

  background-color: $color-whitepale-light;
  position: relative;
  text-align: center;
  overflow: hidden;

  p {
    @include font(16, $letterSpacing: $font-ls-small);
    @include font-notosanssc-medium;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border-left: 20px solid #aa0a3c;
    border-top: 20px solid #fff;
    right: 0;
    top: 0;
  }
}

.article-nav {
  // @extend %l-widget-spacing;
  background-color: $color-grey-50;
  position: relative;
  padding: 0px 0 8px;
  border-radius: 4px;

  .nav-tag {
    height: 28px;
    width: 74px;
    background-color: $background-success;
    text-align: center;
    @include font(14);
    color: $color-blue-900;
    border-radius: 4px 0 16px 0;

    &.right {
      border-radius: 0 4px 0 16px;
    }
  }

  a {
    &:hover {
      color: $color-grey;
      text-decoration: none;
    }

    .article-nav-link {
      margin-bottom: 0;
      @include font(16);
    }
  }

  .fas {
    color: $color-brand;
  }

  .divider-border {
    position: absolute;
    left: calc(50% - 1px);
    top: 36px;
    bottom: 0;
    background-color: $color-grey-lighter;
    width: 1px;
    height: 45%;
    margin: auto;
  }
}
