// =====================================================================
// Topnavbar (Mobile)
// =====================================================================

.topnavbar-mobile {
  position: relative;
  z-index: 3;
  background-color: $color-white;

  .topnav-link {
    @extend %l-topnav-link-padding-y;
  }

  .hamburger {
    @include hamburger($ham-w, $ham-h, 2px, $color-grey-light);

    > div {
      border-radius: 1.5px;
    }
  }

  .topnavbar-header {
    transition: box-shadow 0.7s $easeOutCubic;
    position: relative;
    height: $topnavbar-header-h;

    .is-scrolled-down & {
      box-shadow: 0 1px 7px 0 rgba($color-whitepale, 0.5);
    }

    > .container {
      height: 100%;

      > .row {
        height: 100%;
      }
    }

    .mobile-logotype {
      display: none;
      width: 24px;
      height: auto;
      margin-left: 16px;
      // position: absolute;
      // left: 16%;
      // top: 10%;
    }

    .logotype {
      position: relative;
      display: flex;
      transition: transform 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);

      .svg-logotype-type {
        transition: visibility 0.4s 0.3s $easeOutCubic, opacity 0.4s 0.3s $easeOutCubic;
        visibility: visible;
        opacity: 1;
      }

      svg {
        height: 30px;
      }

      .confetti {
        position: absolute;
        top: -22px;
        height: 60px;
        width: 100%;
        overflow: hidden;
        // background-color: red;
        .animation {
          display: block;
          width: 100% !important;
          height: 300px !important;
          transform: translateY(-128px) !important;
        }
      }
    }

    .mobile-text-links {
      transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
      text-align: center;
      position: absolute;
      visibility: hidden;
      opacity: 0;
      height: 40px;
      margin-top: -20px;
      top: 50%;
      left: 50%;
      transform: translateX(-50%);
      width: 220px;
    }

    .mobile-text-links-list {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      a {
        @include font(13);
        color: $color-brand;
      }
    }

    .mobile-text-links-item {
      transition: visibility 0.5s $easeInOutCubic, opacity 0.5s $easeInOutCubic;
      width: 100%;
      flex: 0 1 auto;
      position: absolute;
      opacity: 0;
      visibility: hidden;

      &.is-active {
        position: relative;
        opacity: 1;
        visibility: visible;
      }

      a {
        // @include truncate;
        width: 100%;

        @include font(11);
        @include media-breakpoint-up(sm) {
          @include font(13);
        }
      }
    }

    .cta-holder-subscribe {
      display: flex;
      align-items: center;
      padding-top: 10px;

      a {
        min-width: 0;
        letter-spacing: normal;
        @include font(13);
        @include media-breakpoint-up(sm) {
          @include font(13);
        }
      }
    }

    .social-dropdown,
    .fontsizing-dropdown {
      display: none;
      flex-direction: column;
      text-align: center;
      // width: 56px;
      padding: 0 8px;
      align-items: center;

      @include media-breakpoint-down (sm) {
        // width: 35px;
      }

      &.dropdown {
        .dropdown-menu {
          border-radius: 20px;
          border: 1px solid $color-whitepale;
          width: 56px;
          background-color: $color-white;
          min-width: unset;
          padding: 0;
          top: 55px !important;
          right: -9px;
          left: unset !important;

          @include media-breakpoint-down (sm) {
            // width: 35px;
          }

          .topnav-submenu {
            column-count: 1;
          }

          &.show {
            height: max-content;
            // display: flex;
            // flex-direction: column;
          }

          .dropdown-item {
            width: 100%;
            text-align: center;
            height: 40px;
            border-radius: 100px;
            // padding: 0;

            &:hover {
              background-color: $color-whitepale;
            }
            // padding: 10px 18px;
          }
        }
      }

      .topnav-link {
        padding: 8px 0 0 0;
      }
    }

    .social-dropdown {
      margin-right: 20px;
    }

    .fontsizing-dropdown {
      .dropdown-menu {
        .dropdown-item {
          padding: 8px 0 0 0;
          @include font(16);
          color: $color-grey-light;

          &:hover {
            color: $color-grey
          }

          &.is-active {
            background-color: $color-whitepale;
            color: $color-grey
          }
        }
      }
    }
  }

  .topnavbar-body {
    position: relative;
    z-index: 1;
    background-color: $background-tertiary;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    // backface-visibility: hidden;
    // transform-style: preserve-3d;
    // will-change: transform;
    // transform: translateZ(0);

    @include media-breakpoint-up(lg) {
      overflow: visible;
      max-height: none;
    }

    .navbar-collapse {
      height: 100vh;
    }
    // mobile use profile dashboard
    .dropdown-menu {
      &.collapse {
        &.show {
          // + body {
          //   overflow: hidden;
          // }
        }
      }

      .topnav-submenu {
        column-count: 1;
        padding: 0;

        .topnav-link {
          padding: 0;

          .dashboard {
            position: relative;
            text-align: center;
            padding-top: 48px;
            height: 100vh;
            width: 100vw;

            .dashboard-header {
              // text-align: center;
              p {
                @include font (18);
                font-weight: 500;
              }
            }

            .dashboard-subheader {
              // text-align: center;
              p {
                @include font (14);
                color: $color-grey-light;
                // margin-bottom: 0;
              }
            }

            p {

            }

            .dashboard-list {
              margin: 16px 20% 0;

              li {
                display: flex;
                flex-direction: row;
                align-items: center;
                text-align: left;
                margin-bottom: 16px;

                img {
                  margin-right: 8px;
                }

                p {
                  @include font(14);
                  // margin-left: 6px;
                  margin-bottom: 0;
                }

                a {
                  border-bottom: 1px solid $color-whitepale;
                  margin-bottom: 8px;
                  padding-bottom: 16px;
                  width: 100%;
                }

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }

            .dashboard-login {
              padding-top: 24px;

              .cta-solid-red {
                @include font (16);
                padding: 10px 24px;
                width: 160px;
                border-radius: 100px;
                line-height: 0.8;
              }

              .cta-grey {
                @include font (16);
                padding: 10px 24px;
                height: 36px;
                border-radius: 100px;
                line-height: 0.8;
                width: 100px;
              }
            }

            &.user-login,
            &.user-subscriber {
              padding: 14px 0 0;

              .dashboard-header {
                display: flex;
                align-items: center;

                img {
                  width: 60px;
                  margin-left: 0;
                }

                p {
                  text-align: left;
                }

                .profile-state {
                  text-align: left;
                  margin-left: 16px;

                  .label {
                    @include font (14);
                    letter-spacing: 1px;
                    font-weight: 500;
                  }
                }
              }

              .dashboard-list {
                margin: 16px 0 0;
              }
            }

            .dashboard-title {
              padding: 8px 16px 0;
            }

            .dashboard-msg {
              position: fixed;
              bottom: 45px;
              width: 100%;
              padding: 16px 0;
              background-color: $color-white;
              left: 0;

              p {
                color: $color-grey-light;
                @include font (14);
                margin-bottom: 0;
              }

              a {
                @include font (14);
                color: $content-quaternary;
                border-bottom: 1px solid $content-quaternary;
              }
            }
          }
        }
      }
    }
  }

  .topnavbar-search {
    padding: 15px 0;

    .svg-icon-search {
      position: absolute;
      top: 0;
      right: 12px;
      padding: 0;
      width: 16px;
      fill: $color-brand;
      stroke: $color-brand;
    }

    input[type='search'] {
      // @include font (16);
      line-height: 22px;
    }
  }

  .topnavbar-featured {
    padding: 16px 5px 12px;
    text-align: center;

    .topnav-link {
      @include font(16, $isEm: true);
      padding: 0;
      font-weight: 400;
      color: $color-grey-light;
    }
  }

  .topnavbar-fontsizing {
    padding: 12px 5px 16px;
  }

  .topnavbar-news-list,
  .topnavbar-login {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .topnavbar-news-list {
    li {
      &:hover {
        background: $color-whitepale-light;

        .fa-caret-down {
          color: $color-brand;
        }

        .topnav-link {
          // color: $color-brand;
        }
      }

      .topnav-link {
        font-weight: 400;
        padding: 9px 20px 9px 0;
        width: 100%;
      }

      border-bottom: 1px solid $color-whitepale;
      margin: 0 20px;

      &::after {
        // content: '';
        // display: block;
        // height: 1px;
        // background-color: $color-whitepale;
        // margin: 0 20px;
        // width: 100%;
      }

      &:last-child {
        &::after {
          content: '';
          display: none;
          height: 1px;
          background-color: $color-whitepale;
          margin: 0 20px;
          width: 100%;
        }
      }
    }
  }

  .topnav-link-listing {
    .no-touchevents & {
      &:hover {
        color: $color-white; // to overwrite mobile behaviour
      }
    }
  }

  .user {
    // background: $white;
    &.user-dropdown {
      padding-right: 20px;

      .dropdown-toggle {
        text-align: center;
        background: $white;

        .fa-caret-down {
          float: none;
          display: inline-block;
        }
      }

      .topnav-link {
        // padding-top: 20px;
        // padding-bottom: 20px;
      }

      &:hover,
      :active {
        .welcome,
        .username,
        .fa-caret-down {
          color: $color-white;
        }
      }
    }
  }

  .dropdown {
    cursor: pointer;

    .no-touchevents & {
      &:hover {
        .topnav-link-listing {
          // background-color: $color-whitepale-light;
          color: $color-brand;
        }

        .dropdown-toggle {
          // box-shadow: none;
        }

        .fa-caret-down {
          // color: $color-brand;
        }
      }
    }

    &.show {
      .topnav-link-listing {
        background-color: $color-whitepale-light;
        color: $color-brand;
      }
    }
  }

  .dropdown-toggle {
    width: 100%;

    &[aria-expanded='true'] {
      // background-color: $color-brand !important;
      // color: $color-brand;
      // .welcome {
      //   color: $white;
      // }

      .fa-caret-down {
        transform: rotate3d(1, 0, 0, 180deg);
        color: $color-brand;
      }
    }

    &::after {
      display: none;
    }

    .fa-caret-down {
      transition: transform 0.5s $easeOutCubic;
      float: right;
      @include font(14);
      margin-top: 10px; // agaration
      // margin-right: 20px;
      color: $color-grey;
    }
  }

  .dropdown-menu {
    // prevent -webkit-overflow-scrolling: touch from flickering
    top: auto; // !important; // stylelint-disable-line declaration-no-important
    left: auto; // !important; // stylelint-disable-line declaration-no-important
    overflow: hidden;
  }

  .divider {
    // width: calc(100% - 20px);
    height: 1px;
    margin: 0 auto;
    background-color: $color-brand;
  }

  .topnavbar-news {
    width: 100%;

    .topnav-link {
      @include font(16, $isEm: true);
    }

    .dropdown-menu {
      @include font(18, $isEm: true); // reset bootstrap definition
    }

    .topnav-submenu {
      @include font(10, $isEm: true); // unset compounding
      column-count: 3;

      .topnav-link {
        @include font(9, $isEm: true); // unset compounding, agar to 16px
      }
    }
  }

  .topnavbar-version {
    margin: 0 -15px;

    .topnav-link {
      padding: 9px 20px;
      display: flex;
      align-items: center;
      font-weight: 400;
      @include font (16);

      .fa-caret-down {
        margin-top: 0;
        padding: 10px;
        @include font(14);
        color: $color-grey-light;
      }
    }

    .dropdown-menu {
      padding-left: 20px;
      .topnav-submenu {
        column-count: 1;
        .topnav-link {
          padding: 9px 0;
        }
      }
    }

    .dropdown-toggle {
      &[aria-expanded='true'] {
        // background-color: $color-brand !important;
        // color: $color-brand;
        // .welcome {
        //   color: $white;
        // }

        .topnav-link {
          color: $color-brand;
        }

        .fa-caret-down {
          transform: rotate3d(1, 0, 0, 180deg);
          color: $color-brand;
        }
      }

      &[aria-expanded='false'] {
        color: $color-grey-light;
      }
    }

    .cta-bordered {
      padding: 0 24px;
      position: absolute;
      right: 20px;
      top: 11px;
      line-height: 1.5;
    }
  }

  .topnavbar-login {
    // margin-bottom: $topnavbar-mobile-allowance-mb;
    text-align: center;
    padding: 20px 0;

    .topnav-link {
      @include font(16, $letterSpacing: 0, $isEm: true);
      height: 36px;
      border-radius: 30px;
      min-width: 100px;
    }

    .topnav-link-cancel-padding {
      padding: 2px 16px;
    }
  }

  .topnavbar-user {
    // display: none;
    // background: $color-whitepale;
    // margin-bottom: $topnavbar-mobile-allowance-mb;

    // .dropdown-menu {
    //   @include font(18, $isEm: true); // reset bootstrap definition
    // }

    // .topnav-link {
    //   @include font(12, $letterSpacing: 0, $isEm: true);
    // }

    // .topnav-submenu {
    //   @include font(10, $isEm: true); // unset compounding

    //   .topnav-link {
    //     @include font(9, $isEm: true); // unset compounding, agar to 16px
    //   }
    // }
  }
}

.topnavbar-search {
  .searchbar {
    position: relative;
    width: 100%;
    height: 22px;

    &::after {
      content: '';
      display: block;
      height: 1px;
      background: $color-brand;
      margin-top: 12px;
    }
  }

  input {
    &[type='search'] {
      width: 100%;
      height: 100%;
      background-color: $background-tertiary;
      border: 0;
      color: $color-grey;

      @include font(16, 32px);

      &::placeholder {
        @include font(16, $letterSpacing: 0.5px);

        color: transparentize($color-grey-light, 0.5px);
      }
    }
  }
}

.topnavbar-fontsizing {
  text-align: center;

  p {
    color: $color-grey;
  }

  .topnavbar-fontsizing-title {
    // display: inline-block;
    // float: left;
    // padding-top: 3px;

    // p {
    //   margin-bottom: 0;
    //   @include font(16);
    // }
  }

  .fontsizing-range-list {
    display: flex;
    border-radius: 100px;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 0 16px;
    margin: 0 auto 0 16px;
    width: 224px;

    &::before {
      content: ' ';
      display: block;
      position: absolute;
      border: 1px solid $color-whitepale;
      width: 100%;
      height: 110%;
      border-radius: 100px;
      top: -2px;
      left: 0;
      // z-index: 2;
    }

    li {
      display: flex;
      align-items: start;

      &.is-active {
       .cta-bordered {
          padding: 0 16px;
          color: $color-grey;
          font-weight: 400;
          background-color: $color-whitepale-light;
        }
      }
    }

    .cta-bordered {
      border-radius: 100px;
      min-width: 23px;
      @include font (16);
      border: none;
      white-space: nowrap;
      font-weight: 400;
      padding: 0;
      color: $color-grey-light;
      letter-spacing: 0;
    }
  }

  .switch-button {
    float: right;
    display: inline-block;
    @include font(16);
    font-weight: 400;
    margin-right: 0;

    .switch-button-checkbox {
      + {
        .switch-button-label {
          padding: 0;
        }
      }
    }
  }
}
