@mixin icon-search {
  font-family: 'Font Awesome 5 Free', sans-serif;
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  @include font(40);
  color: $color-white;

  &::before {
    content: '\f00e';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

