.figure-media {
  // text-align: center;
  border-radius: 4px;
  cursor: pointer;
  margin: 0 -15px;

  @include media-breakpoint-up (sm) {
    margin: initial;
  }

  .image {
    width: 100%;
    object-fit: contain;
    object-position: center;
    aspect-ratio: 3 / 2;
    background-color: $color-grey-50;
  }

  .figure-media-cta {
    border-radius: 100px;
    background-color: rgba(0, 0, 0, 0.40);
    position: absolute;
    top: 8px;
    right: 8px;
    min-width: 24px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 16px;
      margin: 0 3px;

      &:first-child {
        margin-left: 6px;
      }

      &:last-child {
        margin-right: 6px;
      }
    }
  }
}

.figure-media-gallery {
  transition: visibility 0.4s $easeOutCubic, opacity 0.4s $easeOutCubic;
  position: relative;
  visibility: hidden;
  opacity: 0;

  .zb-gallery-arrow {
    $arrow-w: 26px;
    $line-w: 3px;
    $line-offset: 12px; // agaration

    position: absolute;
    cursor: pointer;
    z-index: 1;
    top: calc(50% - #{$arrow-w});
    width: $arrow-w;
    height: 40px;

    &:hover {
      .line {
        background-color: $color-white;
      }
    }

    &.zb-gallery-arrow-l {
      left: 15px;

      .line {
        &:first-of-type {
          transform: rotate(-45deg);
        }

        &:last-of-type {
          transform: rotate(45deg);
        }
      }
    }

    &.zb-gallery-arrow-r {
      right: 15px;

      .line {
        &:first-of-type {
          transform: rotate(45deg);
        }

        &:last-of-type {
          transform: rotate(-45deg);
        }
      }
    }

    .line {
      transition: background-color 0.4s $easeOutCubic;
      position: absolute;
      width: $arrow-w;
      height: $line-w;
      border-radius: $line-w / 2;
      background-color: $color-whitepale;

      &:first-of-type {
        top: $line-offset;
      }

      &:last-of-type {
        top: calc(100% - #{$line-offset});
      }
    }
  }

  .slick-slide {
    overflow: hidden;
    outline: none;
  }

  &.slick-dotted {
    &.slick-slider {
      margin-bottom: 0;
    }
  }

  .slick-dots {
    bottom: 20px;

    li {
      margin: 0;

      button {
        padding: 0;

        &::before {
          @include font(10);

          color: $color-white;
          opacity: 1;
        }
      }

      &.slick-active {
        button {
          &::before {
            color: $color-brand;
          }
        }
      }
    }
  }

  &.slick-initialized {
    visibility: visible;
    opacity: 1;
  }
}

.figure-media-gallery-overlay {
  cursor: pointer;
}

.figure-media-gallery-nav {
  @include aspect-ratio(3, 2);

  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;

  .zb-gallery-arrow,
  .slick-dots {
    pointer-events: auto;
  }
}

.figure-media-gallery-slide {
  .article-type-link {
    outline: none;
  }
}

.figure-media-gallery-img {
  @include aspect-ratio(3, 2);
  background-color: $color-whitepale-light;
  position: relative;

  &:hover {
    .tint {
      opacity: 1;
      visibility: visible;
    }
  }

  img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }

  .tint {
    transition: opacity 0.3s $easeOutCubic, visibility 0.3s $easeOutCubic;
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color-black, 0.4);
    opacity: 0;
    visibility: hidden;

    @include icon-search;
  }
}

