.inline-scroll-container {
  position: relative;
  width: 100%;
  left: unset;

  @include media-breakpoint-up (lg) {
    position: absolute;
    left: calc(-25% - 32px);
    width: 25%;
  }

  @include min-screen('1050px') {
    left: calc(-33.3333% - 32px);
    width: 33.3333%;
  }

  @include media-breakpoint-up (xl) {
    left: calc(-25% - 32px);
    width: 25%;
  }

  // @include media-breakpoint-up (lg) {
  //   position: absolute;
  //   left: calc(-33.3333% - 32px);
  //   width: 33.3333%;
  // }
}

.inline-recommend {
  @extend %l-inline-m;
  padding: 16px 0;
  background-color: $color-white;
  border-radius: 4px;
  border: 1px solid $color-grey-lighter;
  position: sticky;
  top: 66px;

  @include min-screen('1050px') {
    margin: 0;
    max-width: 100%;
    z-index: 1;
  }

  .standard-card-container {
    display: flex;

    @include min-screen('1050px') {
      display: block;
    }

    &:first-child {
      padding-top: 0;
      border-top: none;
    }

    &:last-child {
      padding-bottom: 0;
    }

    .card-content {
      @include min-screen('1050px') {
        padding-right: 0;
      }
      .card-content-container {
        h3 {
          @include font(8.8888889, $isEm: true);
        }
      }
    }

    .card-image-container {
      @include min-screen('1050px') {
        padding-top: 16px;
        max-width: 80%;
      }

      img {
        @include min-screen('1050px') {
          border-radius: 4px;
        }
      }
    }
  }

  p {
    margin-top: 0;
  }
}

