// =====================================================================
// Outbrain
// =====================================================================

.OUTBRAIN {
  margin-top: 50px;
  margin-bottom: 50px;

  &[data-widget-id='AR_1'] {
    .AR_1 {
      .ob-widget-section {
        margin-top: 50px;
        margin-bottom: 50px;

        &.ob-first {
          margin-top: 0;
          margin-bottom: 50px;
        }

        &.ob-last {
          margin-top: 50px;
          margin-bottom: 0;
        }
      }

      .ob-widget-header {
        @include font(22, $letterSpacing: $font-ls-small);
        @include font-notosanssc-medium;

        color: $color-brand;
        border-bottom: 2px $color-brand solid;
        margin-bottom: 20px;
      }

      .ob-rec-text {
        @include font-notosanssc-regular;
        @include font(18, $letterSpacing: $font-ls-small);

        color: $color-grey;
      }
    }
  }
}

// example styling - START
.outbrain {
  margin-top: 24px;
  margin-bottom: 8px;
  margin-left: auto;
  margin-right: auto;

  @include media-breakpoint-up (lg) {
    // margin-bottom: 24px;
  }

  .heading-border {
    margin-bottom: 15px;
  }

  .section-header {
    padding-bottom: 0;

    @include media-breakpoint-up (lg) {
      padding-bottom: 24px;
    }

    .section-header-title {
      h2 {
        color: $color-grey;
      }
    }

    &::before {
      border-top: 1px solid $color-grey-lighter;
    }
  }

  .outbrain-listing {
    position: relative;
    @include media-breakpoint-up (lg) {
      margin: 0 -15px;
    }

    div {
      &:last-child {
        .outbrain-item  {
          border-bottom: none;
          // padding-bottom: 0;
        }
      }

      @include media-breakpoint-up (lg) {
        &:nth-last-child(-n + 4) {
          .outbrain-item  {
            border-bottom: none;
          }
        }
      }
    }

    &::before {
      @include media-breakpoint-up (lg) {
        position: absolute;
        left: 0;
        display: block;
        content: '';
        padding-left: 16px;
        background-color: white;
        z-index: 1;
        height: 100%;
      }
    }

    &::after {
      @include media-breakpoint-up (lg) {
        position: absolute;
        right: 0;
        display: block;
        content: '';
        padding-left: 16px;
        background-color: white;
        z-index: 1;
        height: 100%;
      }
    }
  }
}

.outbrain-item {
  padding-bottom: 8px;
  padding-top: 16px;
  border-bottom: 1px solid $color-whitepale;
  @include media-breakpoint-up(lg) {
    padding: 0 16px;
    border-bottom: 1px solid $color-whitepale;
    margin-bottom: 16px;
  }
}

.outbrain-img {
  @extend %l-outbrain-mb;
  .img-fluid {
    border-radius: 4px;
    aspect-ratio: 3/2;
  }
}

.outbrain-text {
  padding-left: 10px;
  padding-right: 10px;

  @include media-breakpoint-up(lg) {
    padding-left: 0;
    padding-right: 0;
  }
}

.outbrain-category {
  @extend %l-outbrain-mb;

  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
  }

  p {
    @include font(14, $letterSpacing: $font-ls-regular);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}

.outbrain-title {
  @extend %l-outbrain-mb;

  h2 {
    @include font-notosanssc-regular;
    @include font(18, $letterSpacing: $font-ls-small);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #767676;
    margin-bottom: 0;
  }
}

.outbrain-published-date {
  @extend %l-outbrain-mb;

  p {
    @include font(12);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}

.outbrain-source {
  @extend %l-outbrain-mb;

  p {
    @include font(12);

    color: $color-grey-light;
    margin-bottom: 0;
  }
}
// example styling - END
