// =====================================================================
// Module
// =====================================================================

.heading-border {
  h1,
  h2 {
    @include font-notosanssc-medium;
    @extend %l-aside-item-padding-y;
  }

  a {
    text-decoration: none;
    display: inline-block;
  }

  &.fit {
    padding: 0;

    > [class*='container'] {
      padding: 0;

      > .row {
        margin: 0;

        > .col {
          padding: 0;
        }
      }
    }
  }

  &.heading-border-primary {
    h1,
    h2 {
      color: $color-brand;
      margin-bottom: 0;
      padding-top: 10px;// 38px;
      padding-bottom: 10px;

      @include media-breakpoint-up (md) {
        padding: 10px 0;
      }
    }

    .divider {
      border-top: solid 1px $color-brand;
    }

    &.solid {
      background-color: $color-brand;
      border-top-left-radius: $br-regular;
      border-top-right-radius: $br-regular;

      h1,
      h2 {
        color: $color-white;
      }
    }

    &.banner-section {
      h1,
      h2 {
        color: #b78b2f;

        &::before {
          background-color: $yellow-500;
        }
      }

      .divider {
        border-top: solid 1px $yellow-500;
      }
    }
  }

  &.heading-border-secondary {
    h1,
    h2 {
      color: $color-grey-light;
      margin-bottom: 10px;

      @include font(16);

      margin-bottom: 0;
    }

    .divider {
      border-top: solid 2px $color-whitepale;
    }
  }

  &.heading-border-categories {
    h1,
    h2 {
      margin-right: 20px;
    }

    .heading-border-right {
      $w: 70px;

      float: left;
      max-width: 100%;
      white-space: nowrap;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: $w;
        height: 100%;
        // background-image: linear-gradient(to right, transparentize($color-white, 1), transparentize($color-white, 0));
      }

      @include media-breakpoint-up(xl) {
        white-space: normal;
        padding-right: 0;
      }
    }

    &.heading-border-categories-listing { // for listing page
      margin-bottom: 20px;

      h1,
      h2 {
        @include font(36, $letterSpacing: $font-ls-small);

        margin-right: 30px;
      }

      .heading-border-sublink {
        @include font(18, $letterSpacing: $font-ls-small);

        margin-right: 20px;
      }

      .divider {
        display: none;
      }

      [class='col'] {
        @include media-breakpoint-down(md) {
          flex: 0 0 100%;

          .heading-border-right {
            max-width: 100%;
          }
        }
      }
    }

    &.header-newsletter-signup {
      h2 {
        margin-bottom: 30px;
        @include font(40);
      }

      p {
        margin-top: 1.66667em;
        margin-bottom: 1.66667em;
        letter-spacing: 0.5px;
      }

      margin-bottom: 0;
      @include media-breakpoint-up(md) {
        margin-bottom: 30px;
      }
    }
  }

  .heading-border-right {
    float: right;

    a {
      @include font(16);

      color: $color-grey-light;
    }

    &::after {
      clear: both;
    }
  }

  .heading-border-right-content {
    $rail-y-h: 4px;

    position: relative;
    padding-top: 6px;
    padding-bottom: 6px;

    .ps__thumb-x {
      height: 0;

      @include media-breakpoint-up(lg) {
        height: $rail-y-h;
      }
    }

    .ps__rail-x {
      // background-color: $color-grey-lighter;
      height: 0;

      @include media-breakpoint-up(lg) {
        height: $rail-y-h;
      }

      &:hover,
      &:focus {
        > .ps__thumb-x {
          // background-color: $color-grey-lighter;
          height: 0;

          @include media-breakpoint-up(lg) {
            height: $rail-y-h;
          }
        }
      }

      &.ps--clicking {
        .ps__thumb-x {
          // background-color: $color-grey-lighter;
          height: 0;

          @include media-breakpoint-up(lg) {
            height: $rail-y-h;
          }
        }
      }
    }
  }

  .heading-border-mainlink {
    h1,
    h2 {
      position: relative;

      &::before {
        transition: opacity 0.4s $easeOutCubic, visibility 0.4s $easeOutCubic, height 0.4s $easeOutCubic;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: $color-brand;
        height: 0;
        opacity: 0;
        visibility: hidden;
      }
    }

    &:hover {
      h1,
      h2 {
        &::before {
          height: 2px;
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }

  .heading-border-sublink {
    transition: color 0.4s $easeOutExpo;
    display: inline-block;
    margin-right: 20px;

    &:hover {
      color: $color-grey;
    }
  }

  .divider {
  }

  .heading-border-padding-top {
    padding-top: 30px;
    @include media-breakpoint-up(md) {
      padding-top: 20px;
    }

    .heading-border-right {
      float: none;
      white-space: normal;

      .heading-border-right-content {
        text-align: center;

        p {
          font-weight: 500;
        }

        a {
          color: $color-brand;
          @include font (16);
          font-weight: 500;
          margin: 0 15px;

          .link-underline {
            color: $color-brand;
          }

          .icon-arrow-down {
            width: 13px;
            height: auto;
          }
        }
      }

      .epaper-border-right-content {
        text-align: center;

        p {
          font-weight: 500;
        }

        a {
          color: $color-brand;
          @include font (16);
          font-weight: 500;
          margin: 0;

          .link-underline {
            color: $color-brand;
            @include font (16);

            @include media-breakpoint-up (md) {
              @include font (18);
            }
          }

          .icon-arrow-down {
            width: 13px;
            height: auto;
          }

          @include media-breakpoint-up (md) {
            margin: 0 15px;
          }
        }

        .link-container {
          display: flex;
          justify-content: space-between;

          @include media-breakpoint-up (md) {
            justify-content: center;
          }
        }
      }
    }
  }
}

// NEW CSS FOR FUTURE DESIGN SYSTEM
.section-header {
  width: 100%;
  margin: 0 auto;
  padding-bottom: 24px;
  // height: 42px;
  // overflow-y: hidden;

  &::before {
    content: ' ';
    width: 100%;
    padding-top: 8px;
    display: block;
    height: 1px;
    border-top: 1px solid $color-brand;
  }

  .section-header-title {
    display: flex;
    align-items: center;
    width: fit-content;
    margin-right: 16px;

    .breaking-news-flash {
      height: 16px;
      width: 16px;
      background-color: $color-brand;
      border-radius: 50%;
      margin-right: 8px;

      div {
        border-radius: 50%;
        border: 1px solid $white;
        width: 8px;
        height: 8px;
        margin: 4px;
        animation: blink 500ms linear infinite;
      }
    }

    h2 {
      color: $color-black;
      margin-bottom: 0;
      @include font(24);
    }
  }

  .section-header-link {
    display: block;
    width: 20px;
    height: 20px;
    background-color: $color-brand;
    border-radius: 50%;
    margin-left: 8px;
    margin-bottom: 2px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px 10px;
    background-image: url('./../images/icon/arrow-next-white.svg');
  }

  .heading-right {
    position: relative;
    display: flex;
    justify-content: flex-end;
    width: calc(100% - 142px);
    height: 24px;
    // overflow: hidden;

    @include media-breakpoint-up (sm) {
      justify-content: flex-end;
    }


    &:hover {
      .heading-right-content {
        &::-webkit-scrollbar {
          display: none;
          @include media-breakpoint-up (sm) {
            background: none;
            display: block;
          }

          @include media-breakpoint-up (xl) {
            display: none;
          }
          // border-top: 1px solid $color-whitepale-light;
        }

        &::-webkit-scrollbar-track {

        }

        &::-webkit-scrollbar-thumb {
          // background-color: $color-whitepale-light;
          // border-top: 2px solid $color-whitepale-light;
          // width: 10%;
          }
      }
    }


    .heading-right-content {
      overflow: auto;
      display: flex;
      // position: relative;
      // height: 40px;

      &::-webkit-scrollbar {
        display: none;
        // background-color: red;
      }

      @include media-breakpoint-up (sm) {
        height: 40px;
        overflow-x: scroll;
      }

      @include media-breakpoint-up (xl) {
        overflow-x: auto;
        height: unset;
      }

      .heading-sublink {
        @include font(16);
        color: $color-grey-light;
        padding: 0 12px;
        text-decoration: none;
        white-space: nowrap;

        @include media-breakpoint-up (lg) {
          &:empty {
            display: none;
          }
        }

        &:hover {
          color: $color-grey;
        }

        &:last-child {
          @include media-breakpoint-up (md) {
            padding-right: 0;
          }
        }
      }
    }
  }

  &.breaking-news {
    .section-header-title {
      .breaking-news-flash {
        background-color: #e90044;
      }

      a {
        background-color: #e90044;
      }
    }
  }

  &.inverse {
    &::before {
      content: ' ';
      width: 100%;
      padding-top: 8px;
      display: block;
      height: 1px;
      border-top: 1px solid $white;
    }

    .section-header-title {
      h2 {
        color: $white;
      }
    }

    .section-header-link {
      display: block;
      width: 20px;
      height: 20px;
      background-color: $white;
      border-radius: 50%;
      margin-left: 8px;
      margin-bottom: 2px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 10px;
      background-image: url('./../images/icon/arrow-next-black.svg');
    }
  }

  &.banner-wall-header {
    &::before {
      border-top: 1px solid $yellow-500;
    }

    .section-header-link {
      background-color: $yellow-500;
    }
  }

  &.outbrain {
    .heading-right {
      justify-content: flex-end;
    }
  }
}

@keyframes blink {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
