// =====================================================================
// Newsticker
// =====================================================================

.newsticker {
  transition: transform 0.4s $easeOutCubic;
  background-color: $color-white;
  padding-top: 8px;
  padding-bottom: 8px;

  @include media-breakpoint-up(lg) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.newsticker-content {
  background-image: linear-gradient(to right, $color-grey-light, $color-whitepale);
  border-radius: 100px;

  @include media-breakpoint-up(lg) {
    height: 34px;
    line-height: 32px;
  }

  h3,
  p,
  a {
    color: $color-white;
    margin: 0;
  }

  a {
    display: inline-block;
    vertical-align: middle;
  }

  .divider {
    width: 1px;
    height: 70%;
    background-color: $color-white;
    background-color: #ffffff;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }

  .newsticker-title {
    @include font-notosanssc-medium;

    margin-top: 8px;
    margin-bottom: 8px;
    margin-left: 10px;
    width: 40px;

    @include media-breakpoint-up(lg) {
      margin-top: 0;
      margin-bottom: 0;
      margin-left: 0;
      width: auto;
    }

    @include media-breakpoint-down(md) {
      @include font(16, $letterSpacing: $font-ls-small);
    }
  }

  .newsticker-link-article {
    transition: color 0.4s $easeOutCubic;

    @include font(16);

    &:hover {
      text-decoration: none;
      color: $color-brand;
    }
  }

  .newsticker-link-next {
    transition: color 0.4s $easeOutCubic;
    color: $color-grey-light;

    @include font(14);

    &:hover {
      color: $color-grey;
      text-decoration: none;
    }
  }

  .newsticker-link-list {
    @include reset-list;

    overflow: hidden;
    // min-height: 24px; // agaration
  }

  .newsticker-link-item {
    animation: fade-out-up 0.6s $easeOutCubic forwards;
    transform: translateY(100%);
    position: absolute;
    top: 0;

    &.is-active {
      animation: fade-in-up 0.6s 0.3s $easeOutCubic forwards;
      transform: translateY(0);
      position: relative;
      visibility: hidden;
      opacity: 0;
    }
  }
}
