// =====================================================================
// Selectpicker
// =====================================================================

.bootstrap-select {
  &.show {
    .dropdown-toggle {
      &::after {
        transform: scale(-1);
      }
    }
  }

  .selectpicker {
  }

  .dropdown-toggle {
    &::after {
      transition: transform 0.4s $easeOutCubic;

      @include font(20);
    }
  }

  .filter-option {
    .filter-option-inner-inner {
      @include font(18, $letterSpacing: $font-ls-small);
    }
  }
}
