// =====================================================================
// Group
// =====================================================================
$mobile-items-number: 5;

.group {
  @include media-breakpoint-up(lg) {
    margin-bottom: 20px;
  }

  &:first-of-type {
    margin-top: 0;
  }

  .divider {
    &.divider-x {
      background-color: $color-whitepale;
      width: 100%;
      height: 1px;
    }
  }

  &.group-article-type-divider {
    .article-list {
      > li {
        .article-type {
          border-bottom: 1px solid $color-whitepale;
        }

        &:last-of-type {
          .article-type {
            border-bottom: 0;

            @include media-breakpoint-up(lg) {
              border-bottom: 1px solid $color-whitepale;
            }
          }
        }
      }
    }
  }

  &.group-showmore {
    .article-list {
      > li {
        display: none;
        border-bottom: 0;

        @for $i from 1 through $mobile-items-number {
          &:nth-child(#{$i}) {
            display: block;
          }
        }

        &:nth-child(#{$mobile-items-number}) {
          .article-type {
            border-bottom: 0;
          }
        }

        @include media-breakpoint-up(lg) {
          display: block;

          &:nth-child(#{$mobile-items-number}) {
            .article-type {
              border-bottom: 1px solid $color-whitepale;
            }
          }
        }
      }
    }

    &.is-showmore {
      .article-list {
        > li {
          display: block;

          &:nth-child(#{$mobile-items-number}) {
            .article-type {
              border-bottom: 1px solid $color-whitepale;
            }
          }

          &:last-of-type {
            .article-type {
              border-bottom: 0;

              @include media-breakpoint-up(lg) {
                border-bottom: 1px solid $color-whitepale;
              }
            }
          }
        }
      }

      .group-showmore-link {
        display: none;
      }
    }

    .group-showmore-link {
      @include media-breakpoint-up(lg) {
        display: none;
      }
    }
  }

  .group-showmore-link {
    display: block;

    @include font(14, $letterSpacing: $font-ls-small);

    color: $color-grey-light;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
    text-decoration: underline;
  }
}
